import React, { useEffect } from 'react';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import { useSelector } from 'react-redux';
import { ThemeProvider } from 'styled-components';
import { lightTheme, darkTheme } from './styles/theme';
import './styles/global.css';

// Pages
import HomePage from './pages/HomePage';
import FlashcardPage from './pages/FlashcardPage';
import StudyPage from './pages/StudyPage';
import StudySessionPage from './pages/StudySessionPage';
import CharacterPage from './pages/CharacterPage';
import SettingsPage from './pages/SettingsPage';

// Components
import Header from './components/common/Header';
import Navigation from './components/common/Navigation';

function App() {
  const { theme } = useSelector((state) => state.settings);

  // Add effect to apply theme class to body element
  useEffect(() => {
    // First remove any existing theme classes
    document.body.classList.remove('light-theme', 'dark-theme');
    
    // Then add the appropriate class based on current theme
    document.body.classList.add(`${theme}-theme`);
    
    // Also set a data attribute for easier CSS targeting
    document.body.setAttribute('data-theme', theme);
  }, [theme]);

  const currentTheme = theme === 'dark' ? darkTheme : lightTheme;

  return (
    <ThemeProvider theme={currentTheme}>
      <Router>
        <div className="app">
          <Header />
          <main className="main-content">
            <Routes>
              <Route path="/" element={<HomePage />} />
              <Route path="/flashcards" element={<FlashcardPage />} />
              <Route path="/study" element={<StudyPage />} />
              <Route path="/study/session/:levelId" element={<StudySessionPage />} />
              <Route path="/character" element={<CharacterPage />} />
              <Route path="/settings" element={<SettingsPage />} />
            </Routes>
          </main>
          <Navigation />
        </div>
      </Router>
    </ThemeProvider>
  );
}

export default App;
