import React, { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import styled from 'styled-components';
import { addFlashcard, addCategory } from '../../redux/slices/flashcardsSlice';
import { generateFromWord } from '../../services/aiService';
import ApiResponseDebugger from '../Debug/ApiResponseDebugger';

const AssistedFlashcardCreator = () => {
  const dispatch = useDispatch();
  const { categories } = useSelector(state => state.flashcards);
  const { learningLanguage, apiKey } = useSelector(state => state.settings);
  
  const [word, setWord] = useState('');
  const [category, setCategory] = useState('');
  const [newCategory, setNewCategory] = useState('');
  const [isAddingCategory, setIsAddingCategory] = useState(false);
  const [cardType, setCardType] = useState('classic');
  const [isGenerating, setIsGenerating] = useState(false);
  const [error, setError] = useState(null);
  const [errorDetails, setErrorDetails] = useState(null);
  const [previewData, setPreviewData] = useState(null);
  const [isDebugging, setIsDebugging] = useState(false);
  
  const handleGenerate = async () => {
    if (!word.trim()) {
      setError('Please enter a word or phrase');
      setErrorDetails(null);
      return;
    }

    if (!apiKey) {
      setError('Please add your OpenAI API key in Settings to use this feature.');
      setErrorDetails(null);
      return;
    }
    
    setError(null);
    setErrorDetails(null);
    setIsGenerating(true);
    
    try {
      const data = await generateFromWord(word, learningLanguage);
      setPreviewData(data);
    } catch (error) {
      // Extract error type and message
      const errorParts = error.message.split(': ');
      const errorType = errorParts.length > 1 ? errorParts[0] : 'ERROR';
      const errorMessage = errorParts.length > 1 ? errorParts.slice(1).join(': ') : error.message;
      
      setError(`Generation Failed: ${errorType}`);
      setErrorDetails(errorMessage);
      console.error(`${errorType}: ${errorMessage}`);
      
      // Enable debugging if it's a JSON parse error
      if (errorType === 'JSON_PARSE_ERROR') {
        setIsDebugging(true);
      }
    } finally {
      setIsGenerating(false);
    }
  };
  
  const handleAddFlashcard = () => {
    if (!previewData) return;

    // Ensure sentence example translation exists
    const sentenceExampleTranslation = previewData.sentenceExampleTranslation || 
      `Translation of: ${previewData.sentenceExample}`;

    const newCard = {
      type: cardType,
      front: word,
      back: previewData.back,
      category: category || 'Uncategorized',
      sentenceExample: previewData.sentenceExample || '',
      sentenceExampleTranslation: sentenceExampleTranslation, 
    };
    
    if (cardType === 'multiple') {
      newCard.multipleChoice = previewData.multipleChoice;
    } else if (cardType === 'fill') {
      newCard.sentence = previewData.sentence;
      newCard.answer = previewData.answer;
    }
    
    dispatch(addFlashcard(newCard));
    
    if (category && !categories.includes(category)) {
      dispatch(addCategory(category));
    }
    
    // Reset form
    setWord('');
    setPreviewData(null);
  };
  
  const handleAddCategory = () => {
    if (newCategory.trim()) {
      dispatch(addCategory(newCategory.trim()));
      setCategory(newCategory.trim());
      setNewCategory('');
      setIsAddingCategory(false);
    }
  };

  return (
    <Container>
      <FormGroup>
        <label>
          Word/Phrase in {learningLanguage}:
          <input
            type="text"
            value={word}
            onChange={(e) => setWord(e.target.value)}
            placeholder={`e.g., "hola" in Spanish`}
          />
        </label>
      </FormGroup>
      
      <FormGroup>
        <label>
          Category:
          {!isAddingCategory ? (
            <CategorySelector>
              <select 
                value={category} 
                onChange={(e) => setCategory(e.target.value)}
              >
                <option value="">Select a category</option>
                {categories.map((cat, index) => (
                  <option key={index} value={cat}>{cat}</option>
                ))}
              </select>
              <AddCategoryButton onClick={() => setIsAddingCategory(true)}>
                + New
              </AddCategoryButton>
            </CategorySelector>
          ) : (
            <CategoryInput>
              <input
                type="text"
                value={newCategory}
                onChange={(e) => setNewCategory(e.target.value)}
                placeholder="New category name"
              />
              <ButtonGroup>
                <AddButton onClick={handleAddCategory}>Add</AddButton>
                <CancelButton onClick={() => setIsAddingCategory(false)}>Cancel</CancelButton>
              </ButtonGroup>
            </CategoryInput>
          )}
        </label>
      </FormGroup>
      
      <CardTypeSelector>
        <CardTypeButton 
          active={cardType === 'classic'} 
          onClick={() => setCardType('classic')}
        >
          Anki-style
        </CardTypeButton>
        <CardTypeButton 
          active={cardType === 'multiple'} 
          onClick={() => setCardType('multiple')}
        >
          Multiple Choice
        </CardTypeButton>
        <CardTypeButton 
          active={cardType === 'fill'} 
          onClick={() => setCardType('fill')}
        >
          Fill in the Blank
        </CardTypeButton>
      </CardTypeSelector>
      
      {error && (
        <ErrorContainer>
          <ErrorMessage>{error}</ErrorMessage>
          {errorDetails && <ErrorDetails>{errorDetails}</ErrorDetails>}
          <ErrorTip>
            {error.includes('API_KEY') && 'Check that your API key is entered correctly in Settings.'}
            {error.includes('NETWORK') && 'Check your internet connection and try again.'}
            {error.includes('JSON') && 'The AI provided an invalid response format. Try a different word or phrase.'}
          </ErrorTip>
        </ErrorContainer>
      )}
      
      <GenerateButtonContainer>
        <GenerateButton 
          onClick={handleGenerate}
          disabled={isGenerating || !word.trim()}
        >
          {isGenerating ? 'Generating...' : 'Generate Card Data'}
        </GenerateButton>
      </GenerateButtonContainer>
      
      {previewData && (
        <PreviewSection>
          <PreviewTitle>Preview</PreviewTitle>
          
          <PreviewCard>
            <PreviewRow>
              <PreviewLabel>Front:</PreviewLabel>
              <PreviewText>{word}</PreviewText>
            </PreviewRow>
            
            <PreviewRow>
              <PreviewLabel>Back:</PreviewLabel>
              <PreviewText>{previewData.back}</PreviewText>
            </PreviewRow>
            
            {/* Add example sentence to preview */}
            {previewData.sentenceExample && (
              <PreviewRow>
                <PreviewLabel>Example Sentence:</PreviewLabel>
                <PreviewText>{previewData.sentenceExample}</PreviewText>
              </PreviewRow>
            )}

            {(previewData.sentenceExampleTranslation || previewData.sentenceExample) && (
              <PreviewRow>
                <PreviewLabel>Example Translation:</PreviewLabel>
                <PreviewText>
                  <em>"{previewData.sentenceExampleTranslation || 'Translation pending...'}"</em>
                </PreviewText>
              </PreviewRow>
            )}
            
            {cardType === 'multiple' && (
              <PreviewRow>
                <PreviewLabel>Multiple choice options:</PreviewLabel>
                <OptionsList>
                  {previewData.multipleChoice.options.map((option, index) => (
                    <OptionItem key={index} isCorrect={index === previewData.multipleChoice.correctIndex}>
                      {option} {index === previewData.multipleChoice.correctIndex && '✓'}
                    </OptionItem>
                  ))}
                </OptionsList>
              </PreviewRow>
            )}
            
            {cardType === 'fill' && (
              <PreviewRow>
                <PreviewLabel>Example sentence:</PreviewLabel>
                <PreviewText>
                  {previewData.sentence.replace('___', `_____ (${previewData.answer})`)}
                </PreviewText>
              </PreviewRow>
            )}
            
            <CreateButtonContainer>
              <CreateButton onClick={handleAddFlashcard}>
                Create Flashcard
              </CreateButton>
            </CreateButtonContainer>
          </PreviewCard>
        </PreviewSection>
      )}
      
      {!apiKey && (
        <NoteText>
          Note: You need to add your OpenAI API key in Settings to use this feature.
        </NoteText>
      )}
      
      <ApiResponseDebugger 
        enabled={isDebugging} 
        word={word}
        learningLanguage={learningLanguage}
      />
    </Container>
  );
};

const Container = styled.div`
  background: ${props => props.theme.backgroundLight};
  border-radius: 8px;
  padding: 20px;
  margin-bottom: 20px;
`;

const FormGroup = styled.div`
  margin-bottom: 16px;
  
  label {
    display: block;
    margin-bottom: 6px;
    font-weight: 500;
  }
  
  input, select, textarea {
    width: 100%;
    padding: 10px;
    border: 1px solid ${props => props.theme.border};
    border-radius: 4px;
    font-size: 16px;
    background: ${props => props.theme.background};
    color: ${props => props.theme.text};
  }
`;

const CardTypeSelector = styled.div`
  display: flex;
  margin-bottom: 20px;
  border-radius: 6px;
  overflow: hidden;
  border: 1px solid ${props => props.theme.border};
`;

const CardTypeButton = styled.button`
  background: ${props => props.active ? props.theme.primary : 'transparent'};
  color: ${props => props.active ? 'white' : props.theme.text};
  border: none;
  padding: 10px 0;
  flex: 1;
  cursor: pointer;
  transition: background 0.3s;
  font-weight: ${props => props.active ? '600' : '400'};
  
  &:hover {
    background: ${props => props.active ? props.theme.primary : props.theme.backgroundDark};
  }
`;

const CategorySelector = styled.div`
  display: flex;
  gap: 10px;
  
  select {
    flex: 1;
  }
`;

const CategoryInput = styled.div`
  display: flex;
  flex-direction: column;
  gap: 10px;
`;

const ButtonGroup = styled.div`
  display: flex;
  gap: 10px;
`;

const AddCategoryButton = styled.button`
  padding: 10px;
  background: ${props => props.theme.accent};
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background 0.3s;
  
  &:hover {
    background: ${props => props.theme.accentDark};
  }
`;

const AddButton = styled.button`
  padding: 8px 16px;
  background: ${props => props.theme.primary};
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  flex: 1;
`;

const CancelButton = styled.button`
  padding: 8px 16px;
  background: transparent;
  border: 1px solid ${props => props.theme.border};
  border-radius: 4px;
  cursor: pointer;
  flex: 1;
`;

const GenerateButtonContainer = styled.div`
  margin-top: 20px;
  text-align: center;
`;

const GenerateButton = styled.button`
  padding: 12px 24px;
  background: ${props => props.disabled ? '#ccc' : props.theme.accent};
  color: white;
  border: none;
  border-radius: 4px;
  font-size: 16px;
  font-weight: 600;
  cursor: ${props => props.disabled ? 'not-allowed' : 'pointer'};
  transition: background 0.3s;
  
  &:hover {
    background: ${props => props.disabled ? '#ccc' : props.theme.accentDark};
  }
`;

const ErrorContainer = styled.div`
  color: ${props => props.theme.error};
  padding: 12px;
  background-color: ${props => `${props.theme.error}15`};
  border-radius: 4px;
  margin-bottom: 16px;
  border-left: 4px solid ${props => props.theme.error};
`;

const ErrorMessage = styled.div`
  font-weight: 600;
  margin-bottom: 4px;
`;

const ErrorDetails = styled.div`
  font-size: 14px;
  margin-bottom: 8px;
  font-family: monospace;
  white-space: pre-wrap;
  word-break: break-word;
`;

const ErrorTip = styled.div`
  font-size: 13px;
  font-style: italic;
  margin-top: 6px;
  color: ${props => props.theme.textLight};
`;

const PreviewSection = styled.div`
  margin-top: 24px;
  border-top: 1px solid ${props => props.theme.border};
  padding-top: 24px;
`;

const PreviewTitle = styled.h3`
  font-size: 18px;
  margin-bottom: 16px;
  color: ${props => props.theme.text};
`;

const PreviewCard = styled.div`
  background: ${props => props.theme.background};
  border-radius: 8px;
  padding: 16px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
`;

const PreviewRow = styled.div`
  margin-bottom: 12px;
`;

const PreviewLabel = styled.div`
  font-weight: 600;
  margin-bottom: 4px;
  color: ${props => props.theme.textLight};
  font-size: 14px;
`;

const PreviewText = styled.div`
  font-size: 16px;
  padding: 8px 0;
`;

const OptionsList = styled.ul`
  list-style-type: none;
  padding: 0;
  margin: 0;
`;

const OptionItem = styled.li`
  padding: 6px 0;
  font-weight: ${props => props.isCorrect ? '600' : '400'};
  color: ${props => props.isCorrect ? props.theme.success : props.theme.text};
`;

const CreateButtonContainer = styled.div`
  margin-top: 20px;
  text-align: right;
`;

const CreateButton = styled.button`
  padding: 10px 20px;
  background: ${props => props.theme.primary};
  color: white;
  border: none;
  border-radius: 4px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: background 0.3s;
  
  &:hover {
    background: ${props => props.theme.primaryDark};
  }
`;

const NoteText = styled.p`
  font-size: 14px;
  color: ${props => props.theme.textLight};
  font-style: italic;
  margin-top: 16px;
`;

export default AssistedFlashcardCreator;
