import { store } from '../redux/store';

const getEffectiveApiKey = () => {
  const { apiKey } = store.getState().settings;
  return apiKey || process.env.REACT_APP_DEFAULT_OPENAI_KEY || '';
};

export const generateFlashcards = async (prompt, count = 5, cardTypes = ['classic', 'multiple', 'fill']) => {
  const { learningLanguage } = store.getState().settings;
  
  // Use the helper function to get the effective API key
  const effectiveApiKey = getEffectiveApiKey();
  
  // More detailed API key validation
  if (!effectiveApiKey) {
    throw new Error('API_KEY_MISSING: OpenAI API key is required. Please add it in the settings or enable "Use default API key".');
  }
  
  if (!effectiveApiKey.startsWith('sk-')) {
    throw new Error('INVALID_API_KEY_FORMAT: The API key doesn\'t appear to be in the correct format. OpenAI keys typically start with "sk-"');
  }
  
  // Modify the token calculation for safety
  const tokensPerCard = 400; // Estimated average tokens per card
  const maxTokens = Math.min(4000, count * tokensPerCard); // Cap at 4000 to stay within API limits
  
  try {
    console.log('Starting API request to generate flashcards...');
    console.log(`Requested card count: ${count}`);
    console.log('Card types requested:', cardTypes);
    
    // Create type-specific instructions based on selected card types
    let typeInstructions = '';
    
    if (cardTypes.includes('classic') && cardTypes.includes('multiple') && cardTypes.includes('fill')) {
      typeInstructions = 'Create a mix of different flashcard types (Anki-style, multiple choice, and fill-in-the-blank).';
    } else {
      const typeNames = [];
      if (cardTypes.includes('classic')) typeNames.push('Anki-style');
      if (cardTypes.includes('multiple')) typeNames.push('multiple choice');
      if (cardTypes.includes('fill')) typeNames.push('fill-in-the-blank');
      
      typeInstructions = `Create only ${typeNames.join(' and ')} flashcards.`;
    }
    
    const response = await fetch('https://api.openai.com/v1/chat/completions', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${effectiveApiKey}`
      },
      body: JSON.stringify({
        model: 'gpt-3.5-turbo',
        messages: [
          {
            role: 'system',
            content: `You are a language learning assistant specializing in creating flashcards for ${learningLanguage}. You will respond ONLY with valid JSON without any additional text. Every example sentence MUST include an accurate English translation.`
          },
          {
            role: 'user',
            content: `Create exactly ${count} flashcards about "${prompt}" for language learning. ${typeInstructions}

For each flashcard, provide the following in a structured format:

1. A "type" field indicating the card type ("classic", "multiple", or "fill")
2. A "front" field with the question/prompt
3. A "back" field with the answer/translation
4. A "sentenceExample" field with a natural example sentence showing word usage
5. A "sentenceExampleTranslation" field with an accurate English translation of the example sentence
6. For multiple-choice cards: include "multipleChoice" with "options" array and "correctIndex"
7. For fill-in-the-blank cards: include "sentence" with ___ where the word goes, and "answer" field

Return the data as a JSON array in this exact format:
{
  "cards": [
    {
      "type": "classic",
      "front": "Word or phrase",
      "back": "Translation or definition",
      "sentenceExample": "An example sentence using the word",
      "sentenceExampleTranslation": "English translation of the example sentence"
    },
    {
      "type": "multiple",
      "front": "Word or phrase",
      "back": "Translation or definition",
      "sentenceExample": "An example sentence using the word",
      "sentenceExampleTranslation": "English translation of the example sentence",
      "multipleChoice": {
        "options": ["correct answer", "incorrect 1", "incorrect 2"],
        "correctIndex": 0
      }
    },
    {
      "type": "fill",
      "front": "Word or phrase",
      "back": "Translation or definition",
      "sentenceExample": "Another example sentence using the word",
      "sentenceExampleTranslation": "English translation of the example sentence",
      "sentence": "A sentence with ___ for the word",
      "answer": "the missing word"
    }
  ]
}

IMPORTANT: Return ONLY the JSON. No markdown formatting, no code blocks, and no additional explanations. Generate exactly ${count} cards, not more and not less. Every card MUST have both sentenceExample and sentenceExampleTranslation fields with accurate translations.`
          }
        ],
        temperature: 0.5,
        max_tokens: maxTokens, // Use our safely calculated token limit
        response_format: { type: "json_object" }
      })
    });

    console.log(`Response status: ${response.status}`);
    
    const responseData = await response.json();
    
    // Handle API-level errors
    if (!response.ok) {
      const errorMessage = responseData.error?.message || 'Unknown API error';
      throw new Error(`API_ERROR: ${errorMessage}`);
    }

    if (!responseData.choices || !responseData.choices[0] || !responseData.choices[0].message) {
      throw new Error('INVALID_RESPONSE: API response missing expected data');
    }

    const content = responseData.choices[0].message.content;
    console.log('Raw content received:', content);
    
    // Enhanced JSON parsing with fallback methods
    try {
      const data = JSON.parse(content);
      
      if (!data.cards || !Array.isArray(data.cards) || data.cards.length === 0) {
        throw new Error('INVALID_DATA_FORMAT: Response does not contain cards array');
      }
      
      // Process each card and ensure translations exist
      const processedCards = await Promise.all(data.cards.map(async card => {
        const cardWithId = {
          ...card,
          id: `${Date.now()}-${Math.random().toString(36).substr(2, 9)}`,
        };
        
        // If we have example sentence but missing translation, get one
        if (card.sentenceExample && (!card.sentenceExampleTranslation || 
            card.sentenceExampleTranslation.startsWith('Translation of:'))) {
          try {
            const translation = await getExampleTranslation(card.sentenceExample, learningLanguage);
            cardWithId.sentenceExampleTranslation = translation;
          } catch (translationError) {
            console.warn('Failed to get sentence translation:', translationError);
            // Fallback to a better placeholder that doesn't look like an actual translation
            cardWithId.sentenceExampleTranslation = "[Translation unavailable]";
          }
        }
        
        return cardWithId;
      }));
      
      console.log(`Successfully processed ${processedCards.length} cards`);
      return processedCards;
    } catch (parseError) {
      console.error('JSON Parse error:', parseError);
      throw new Error(`JSON_PARSE_ERROR: ${parseError.message}. Raw content: ${content.substring(0, 100)}...`);
    }
  } catch (error) {
    // Log the full error
    console.error('Error generating flashcards:', error);
    
    // If it's already a structured error we threw, pass it through
    if (error.message.includes(':')) {
      throw error;
    }
    
    // Handle network errors
    if (error.name === 'TypeError' && error.message === 'Failed to fetch') {
      throw new Error('NETWORK_ERROR: Failed to connect to the OpenAI API. Check your internet connection.');
    }
    
    // Handle other errors
    throw new Error(`ERROR: ${error.message}`);
  }
};

/**
 * Get translation for an example sentence
 */
const getExampleTranslation = async (sentence, fromLanguage) => {
  const effectiveApiKey = getEffectiveApiKey();
  
  try {
    console.log(`Getting translation for: "${sentence}"`);
    
    const response = await fetch('https://api.openai.com/v1/chat/completions', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${effectiveApiKey}`
      },
      body: JSON.stringify({
        model: 'gpt-3.5-turbo',
        messages: [
          {
            role: 'system',
            content: `You are a translation assistant. Translate accurately and concisely. 
            Respond ONLY with the translation, no additional comments.`
          },
          {
            role: 'user',
            content: `Translate this ${fromLanguage} text to English: "${sentence}"`
          }
        ],
        temperature: 0.3,
        max_tokens: 100
      })
    });

    const data = await response.json();
    const translation = data.choices[0].message.content.trim();
    
    // Clean the translation in case the model added quotes or "Translation: " prefix
    return translation.replace(/^["']|["']$/g, '').replace(/^Translation:\s*/i, '');
  } catch (error) {
    console.error('Translation error:', error);
    throw error;
  }
};

/**
 * Generate flashcards in batches to handle larger requests
 */
export const generateFlashcardsInBatches = async (prompt, totalCount = 5, cardTypes = ['classic', 'multiple', 'fill'], progressCallback = null) => {
  const batchSize = 8; // Maximum safe batch size to avoid token limits
  const batches = Math.ceil(totalCount / batchSize);
  let allCards = [];
  let existingPrompts = new Set(); // Track existing prompts to avoid duplicates
  
  console.log(`Generating ${totalCount} flashcards in ${batches} batches of ${batchSize}`);
  
  for (let batchIndex = 0; batchIndex < batches; batchIndex++) {
    const cardsInThisBatch = (batchIndex === batches - 1) 
      ? totalCount - (batchIndex * batchSize)  // Last batch might be smaller
      : batchSize;
      
    // Calculate progress percentage for this batch
    const batchStartProgress = (batchIndex / batches) * 100;
    const batchEndProgress = ((batchIndex + 1) / batches) * 100;
    
    if (progressCallback) {
      progressCallback({
        currentBatch: batchIndex + 1,
        totalBatches: batches,
        progress: batchStartProgress,
        message: `Generating batch ${batchIndex + 1} of ${batches} (${cardsInThisBatch} cards)`
      });
    }
    
    try {
      // Create a unique context for this batch to reduce duplicates
      const batchPrompt = `${prompt} (batch ${batchIndex + 1}/${batches}, focusing on ${
        batchIndex === 0 ? "introductory" : 
        batchIndex === batches - 1 ? "advanced" : 
        "intermediate"
      } concepts)`;
      
      // Generate this batch
      const batchCards = await generateFlashcards(
        batchPrompt,
        cardsInThisBatch,
        cardTypes
      );
      
      // Filter out any duplicate cards (checking by front text)
      const uniqueCards = batchCards.filter(card => {
        // Create a simplified key for comparison (just the front text, lowercased)
        const cardKey = card.front.toLowerCase();
        if (existingPrompts.has(cardKey)) {
          return false; // Duplicate, filter it out
        }
        
        existingPrompts.add(cardKey);
        return true;
      });
      
      console.log(`Batch ${batchIndex + 1} generated ${uniqueCards.length} unique cards out of ${batchCards.length}`);
      
      // Add unique cards to our collection
      allCards = [...allCards, ...uniqueCards];
      
      // Report progress after batch completes
      if (progressCallback) {
        progressCallback({
          currentBatch: batchIndex + 1,
          totalBatches: batches,
          cardsGenerated: allCards.length,
          totalCards: totalCount,
          progress: batchEndProgress,
          message: `Generated ${allCards.length} of ${totalCount} cards`
        });
      }
    } catch (error) {
      console.error(`Error generating batch ${batchIndex + 1}:`, error);
      // Continue with next batch even if this one fails
      if (progressCallback) {
        progressCallback({
          currentBatch: batchIndex + 1,
          totalBatches: batches,
          progress: batchEndProgress,
          error: error.message,
          message: `Error in batch ${batchIndex + 1}: ${error.message}`
        });
      }
    }
  }
  
  console.log(`Generation complete: ${allCards.length} total cards generated`);
  
  // If we didn't get the requested number of cards but have some, return what we got
  if (allCards.length > 0 && allCards.length < totalCount) {
    console.log(`Warning: Requested ${totalCount} cards but only generated ${allCards.length} unique cards`);
  }
  
  return allCards;
};

/**
 * Extract text between two marker strings
 * @param {string} text - The text to extract from
 * @param {string} startMarker - The starting marker string
 * @param {string|null} endMarker - The ending marker string (or null to get everything after startMarker)
 * @returns {string} The extracted text
 */
const extractField = (text, startMarker, endMarker) => {
  if (!text) return '';
  
  const startIndex = text.indexOf(startMarker);
  if (startIndex === -1) return '';
  
  const valueStartIndex = startIndex + startMarker.length;
  
  if (!endMarker) {
    return text.substring(valueStartIndex).trim();
  }
  
  const endIndex = text.indexOf(endMarker, valueStartIndex);
  if (endIndex === -1) {
    return text.substring(valueStartIndex).trim();
  }
  
  return text.substring(valueStartIndex, endIndex).trim();
};

export const generateFromWord = async (word, learningLanguage) => {
  const effectiveApiKey = getEffectiveApiKey();
  
  // More detailed API key validation
  if (!effectiveApiKey) {
    throw new Error('API_KEY_ERROR: OpenAI API key is required');
  }
  
  if (!effectiveApiKey.startsWith('sk-')) {
    throw new Error('API_KEY_ERROR: Invalid OpenAI API key format');
  }
  
  try {
    const response = await fetch('https://api.openai.com/v1/chat/completions', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${effectiveApiKey}`
      },
      body: JSON.stringify({
        model: 'gpt-3.5-turbo',
        messages: [
          {
            role: 'system',
            content: `You are a language learning assistant that creates flashcards. Always provide the translation, pronunciation (if applicable), and an example sentence with its English translation. The example sentence and its translation are REQUIRED for every response. Format your response as follows:
            
            Translation: [translation]
            Example Sentence: [example sentence]
            English Translation: [translation of the example]
            
            Do not deviate from this format.`
          },
          {
            role: 'user',
            content: `Create a flashcard for the ${learningLanguage} word/phrase: "${word}". Include: 
            1. The translation into English
            2. An example sentence using this word/phrase 
            3. An English translation of that example sentence
            
            All three elements are required. The example sentence MUST have a translation.`
          }
        ],
        temperature: 0.7
      })
    });

    if (!response.ok) {
      const error = await response.json();
      throw new Error(`API_ERROR: ${error.error?.message || 'Unknown API error'}`);
    }

    const data = await response.json();
    const content = data.choices[0].message.content;

    try {
      // Extract information from the response
      const back = extractField(content, 'Translation:', 'Example Sentence:');
      let sentenceExample = extractField(content, 'Example Sentence:', 'English Translation:');
      if (!sentenceExample) {
        sentenceExample = extractField(content, 'Example:', 'English Translation:');
      }
      
      // Extract the English translation of the example sentence
      let sentenceExampleTranslation = extractField(content, 'English Translation:', null);
      if (!sentenceExampleTranslation) {
        sentenceExampleTranslation = extractField(content, 'Translation of Example:', null);
      }

      // Check if we're missing either the example or translation
      if (!sentenceExample || !sentenceExampleTranslation || 
          sentenceExampleTranslation.includes('Translation of:')) {
        
        // Make a follow-up API call to generate the missing information
        const missingInfo = await generateMissingExampleInfo(word, learningLanguage, back, sentenceExample, sentenceExampleTranslation);
        
        // Update with the generated information
        if (!sentenceExample) sentenceExample = missingInfo.sentenceExample;
        if (!sentenceExampleTranslation || sentenceExampleTranslation.includes('Translation of:')) {
          sentenceExampleTranslation = missingInfo.sentenceExampleTranslation;
        }
      }
      
      // Final validation - never return a placeholder translation
      if (!sentenceExampleTranslation || sentenceExampleTranslation.includes('Translation of:')) {
        // One last attempt to get a translation
        try {
          sentenceExampleTranslation = await getExampleTranslation(sentenceExample, learningLanguage);
        } catch (e) {
          console.warn('Failed final translation attempt:', e);
          sentenceExampleTranslation = "[Translation unavailable]";
        }
      }

      return {
        back,
        sentenceExample: sentenceExample || `This is an example using the word "${word}".`,
        sentenceExampleTranslation
      };
    } catch (error) {
      console.error('Error parsing AI response:', error);
      throw new Error(`PARSING_ERROR: Failed to parse AI response - ${error.message}`);
    }
  } catch (error) {
    console.error('Error generating flashcard:', error);
    throw error;
  }
};

/**
 * Generate missing example sentence or translation
 * This is called when the main API call fails to provide complete information
 */
const generateMissingExampleInfo = async (word, learningLanguage, translation, existingExample, existingTranslation) => {
  const effectiveApiKey = getEffectiveApiKey();
  
  let prompt = '';
  // If we're missing an example, or we have an example but the translation is a placeholder
  const needsTranslation = existingExample && (!existingTranslation || existingTranslation.includes('Translation of:'));
  
  if (!existingExample) {
    prompt = `Create an example sentence in ${learningLanguage} using the word "${word}" (which means "${translation}" in English) and provide its English translation. Format: Example Sentence: [sentence] English Translation: [translation]`;
  } else if (needsTranslation) {
    prompt = `Translate this ${learningLanguage} sentence to English accurately: "${existingExample}" (it contains the word "${word}" which means "${translation}")`;
  }
  
  try {
    const response = await fetch('https://api.openai.com/v1/chat/completions', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${effectiveApiKey}`
      },
      body: JSON.stringify({
        model: 'gpt-3.5-turbo',
        messages: [
          {
            role: 'system',
            content: `You are a language translation assistant. Provide concise, accurate translations. 
            Respond ONLY with the requested information, no additional text.`
          },
          {
            role: 'user',
            content: prompt
          }
        ],
        temperature: 0.3 // Lower temperature for more predictable results
      })
    });

    const data = await response.json();
    const content = data.choices[0].message.content;
    
    let sentenceExample = existingExample;
    let sentenceExampleTranslation = existingTranslation;
    
    if (!existingExample) {
      sentenceExample = extractField(content, 'Example Sentence:', 'English Translation:');
      sentenceExampleTranslation = extractField(content, 'English Translation:', null);
    } else if (needsTranslation) {
      // If we already had an example, we're just getting the translation
      sentenceExampleTranslation = content
        .replace(/^Translation:\s*/i, '')
        .replace(/^English Translation:\s*/i, '')
        .trim();
    }
    
    return {
      sentenceExample: sentenceExample || `This is an example using the word "${word}".`,
      sentenceExampleTranslation: sentenceExampleTranslation || "[Translation unavailable]"
    };
  } catch (error) {
    console.error('Error generating missing example info:', error);
    return {
      sentenceExample: existingExample || `This is an example using the word "${word}".`,
      sentenceExampleTranslation: existingTranslation || "[Translation unavailable]"
    };
  }
};

export const validateApiKey = async (apiKey) => {
  // If apiKey is empty and we're checking default key validity
  const effectiveApiKey = apiKey || process.env.REACT_APP_DEFAULT_OPENAI_KEY || '';
  
  if (!effectiveApiKey) {
    return { valid: false, error: 'No API key provided' };
  }
  
  if (!effectiveApiKey.startsWith('sk-')) {
    return { valid: false, error: 'Invalid API key format. OpenAI keys typically start with "sk-"' };
  }
  
  try {
    // Make a minimal request to check if the API key works
    const response = await fetch('https://api.openai.com/v1/models', {
      headers: {
        'Authorization': `Bearer ${effectiveApiKey}`
      }
    });
    
    const data = await response.json();
    
    if (!response.ok) {
      return { 
        valid: false, 
        error: data.error?.message || 'API request failed' 
      };
    }
    
    return { valid: true };
  } catch (error) {
    return { 
      valid: false, 
      error: error.message || 'Could not validate API key'
    };
  }
};

// Update debugApiRequest to use effectiveApiKey
export const debugApiRequest = async (prompt, mode = 'single', cardTypes = ['classic', 'multiple', 'fill']) => {
  const { learningLanguage } = store.getState().settings;
  
  // Use the helper function
  const effectiveApiKey = getEffectiveApiKey();
  
  if (!effectiveApiKey) {
    return { error: 'API key is missing' };
  }
  
  try {
    const isBatchMode = mode === 'batch';
    
    const systemContent = isBatchMode
      ? `You are a language learning assistant specializing in creating flashcards.`
      : `You are a language learning assistant specializing in ${learningLanguage}.`;
      
    const userContent = isBatchMode
      ? `Create 2 flashcards about "${prompt}" for language learning. Include different types: classic, multiple-choice, and fill-in-the-blank.
        
        Return the data as JSON in this format:
        {
          "cards": [
            {
              "type": "classic or multiple or fill",
              "front": "Word or phrase",
              "back": "Translation or definition",
              ...other fields based on type
            }
          ]
        }`
      : `For the word "${prompt}" in ${learningLanguage}, return data in this format:
        {
          "back": "translation",
          "multipleChoice": {
            "options": ["correct answer", "wrong 1", "wrong 2"],
            "correctIndex": 0
          },
          "sentence": "A ___ sentence.",
          "answer": "word"
        }`;
    
    const response = await fetch('https://api.openai.com/v1/chat/completions', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${effectiveApiKey}`
      },
      body: JSON.stringify({
        model: 'gpt-3.5-turbo',
        messages: [
          {
            role: 'system',
            content: systemContent
          },
          {
            role: 'user',
            content: userContent
          }
        ],
        temperature: 0.5,
        response_format: { type: "json_object" }
      })
    });
    
    const data = await response.json();
    return data;
  } catch (error) {
    return { error: error.message };
  }
};
