import { createSlice, createAction } from '@reduxjs/toolkit';
import { loadFromLocalStorage, saveToLocalStorage } from '../../services/localStorageService';

export const setDictionaryState = createAction('settings/setDictionaryState');

const initialState = loadFromLocalStorage('settings') || {
  theme: 'dark', // Changed default from 'light' to 'dark'
  appLanguage: 'en',
  learningLanguage: 'es',
  difficulty: 'medium',
  cardType: 'all',
  reviewsPerDay: 20,
  apiKey: '',
  soundEffects: true,
  notifications: true,
  showFurigana: true, // New setting for furigana display
  cardAdvanceMode: 'auto', // Options: 'manual', 'autoCorrect', 'auto'
  spacedRepetition: {
    easyInterval: 4,
    mediumInterval: 2,
    hardInterval: 1,
    minEaseFactor: 1.3,
    maxEaseFactor: 2.5
  },
  dictionary: {
    installed: false,
    loading: false,
    error: null,
    lastChecked: null
  }
};

const settingsSlice = createSlice({
  name: 'settings',
  initialState,
  reducers: {
    updateSettings: (state, action) => {
      const updatedSettings = { ...state, ...action.payload };
      saveToLocalStorage('settings', updatedSettings);
      return updatedSettings;
    },
    updateSpacedRepetitionSettings: (state, action) => {
      state.spacedRepetition = {
        ...state.spacedRepetition,
        ...action.payload
      };
      saveToLocalStorage('settings', state);
    },
    setApiKey: (state, action) => {
      state.apiKey = action.payload;
      saveToLocalStorage('settings', state);
    }
  },
  extraReducers: (builder) => {
    builder
      .addCase(setDictionaryState, (state, action) => {
        state.dictionary = {
          ...state.dictionary,
          ...action.payload,
          lastChecked: new Date().toISOString()
        };
      });
  }
});

export const { updateSettings, updateSpacedRepetitionSettings, setApiKey } = settingsSlice.actions;
export default settingsSlice.reducer;
