import React, { useState, useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import styled from 'styled-components';
import { updateSettings, updateSpacedRepetitionSettings, setApiKey } from '../redux/slices/settingsSlice';
import { validateApiKey } from '../services/aiService';
import { isDictionaryInstalled, downloadDictionary } from '../services/dictionaryLoader';
import DictionaryManagement from '../components/Japanese/DictionaryManagement';

const SettingsPage = () => {
  const dispatch = useDispatch();
  const settings = useSelector(state => state.settings);
  
  const [apiKeyVisible, setApiKeyVisible] = useState(false);
  const [tempApiKey, setTempApiKey] = useState(settings.apiKey);
  const [apiKeyValidation, setApiKeyValidation] = useState(null);
  const [isValidating, setIsValidating] = useState(false);
  
  const [dictionaryStatus, setDictionaryStatus] = useState({ 
    installed: false, 
    loading: false,
    progress: 0,
    message: ''
  });
  
  const handleUpdateSetting = (key, value) => {
    dispatch(updateSettings({ [key]: value }));
  };
  
  const handleUpdateSpacedRepetition = (key, value) => {
    dispatch(updateSpacedRepetitionSettings({ [key]: value }));
  };

  // Simplified save API key function without default key toggle
  const handleSaveApiKey = () => {
    if (tempApiKey) {
      // Save the custom API key
      dispatch(setApiKey(tempApiKey));
      
      // Show confirmation message specifically for saving
      setApiKeyValidation({
        valid: true,
        message: "Your API key was saved successfully"
      });
      
      // Clear the message after a delay
      setTimeout(() => setApiKeyValidation(null), 3000);
    }
    
    // Close the input field regardless
    setApiKeyVisible(false);
  };
  
  const handleTestApiKey = async () => {
    setIsValidating(true);
    setApiKeyValidation(null);
    
    try {
      const result = await validateApiKey(tempApiKey || settings.apiKey);
      
      // If test is successful, set a specific "valid" message
      if (result.valid) {
        setApiKeyValidation({
          ...result,
          message: "API key is valid!"
        });
      } else {
        setApiKeyValidation(result);
      }
    } catch (error) {
      setApiKeyValidation({ valid: false, error: error.message });
    } finally {
      setIsValidating(false);
    }
  };

  useEffect(() => {
    const checkDictionary = async () => {
      const installed = await isDictionaryInstalled();
      setDictionaryStatus(prev => ({ ...prev, installed }));
    };
    
    if (settings.learningLanguage === 'ja') {
      checkDictionary();
    }
  }, [settings.learningLanguage]);
  
  const handleDownloadDictionary = async () => {
    setDictionaryStatus(prev => ({ ...prev, loading: true, progress: 0, message: 'Starting download...' }));
    
    try {
      await downloadDictionary((status) => {
        setDictionaryStatus(prev => ({ 
          ...prev, 
          progress: status.progress,
          message: status.message
        }));
      });
      
      setDictionaryStatus({
        installed: true,
        loading: false,
        progress: 100,
        message: 'Dictionary installed successfully!'
      });
    } catch (error) {
      setDictionaryStatus({
        installed: false,
        loading: false,
        progress: 0,
        message: `Error: ${error.message}`
      });
    }
  };
  
  return (
    <Container>
      <PageTitle>Settings</PageTitle>
      
      <SectionTitle>Application Settings</SectionTitle>
      <SettingsGroup>
        <SettingRow>
          <SettingLabel>Theme</SettingLabel>
          <SettingControl>
            <Select 
              value={settings.theme} 
              onChange={(e) => handleUpdateSetting('theme', e.target.value)}
            >
              <option value="light">Light</option>
              <option value="dark">Dark</option>
            </Select>
          </SettingControl>
        </SettingRow>
        
        <SettingRow>
          <SettingLabel>App Language</SettingLabel>
          <SettingControl>
            <Select 
              value={settings.appLanguage} 
              onChange={(e) => handleUpdateSetting('appLanguage', e.target.value)}
            >
              <option value="en">English</option>
              <option value="es">Spanish</option>
              <option value="fr">French</option>
              <option value="de">German</option>
              <option value="it">Italian</option>
              <option value="pt">Portuguese</option>
            </Select>
          </SettingControl>
        </SettingRow>
        
        <SettingRow>
          <SettingLabel>Sound Effects</SettingLabel>
          <SettingControl>
            <Toggle 
              checked={settings.soundEffects} 
              onClick={() => handleUpdateSetting('soundEffects', !settings.soundEffects)}
            />
          </SettingControl>
        </SettingRow>
        
        <SettingRow>
          <SettingLabel>Notifications</SettingLabel>
          <SettingControl>
            <Toggle 
              checked={settings.notifications} 
              onClick={() => handleUpdateSetting('notifications', !settings.notifications)}
            />
          </SettingControl>
        </SettingRow>
      </SettingsGroup>
      
      <SectionTitle>Study Settings</SectionTitle>
      <SettingsGroup>
        <SettingRow>
          <SettingLabel>Learning Language</SettingLabel>
          <SettingControl>
            <Select 
              value={settings.learningLanguage} 
              onChange={(e) => handleUpdateSetting('learningLanguage', e.target.value)}
            >
              <option value="es">Spanish</option>
              <option value="fr">French</option>
              <option value="de">German</option>
              <option value="it">Italian</option>
              <option value="ja">Japanese</option>
              <option value="ko">Korean</option>
              <option value="zh">Chinese</option>
              <option value="ru">Russian</option>
              <option value="ar">Arabic</option>
            </Select>
          </SettingControl>
        </SettingRow>
        
        {settings.learningLanguage === 'ja' && (
          <>
            <SettingRow>
              <SettingLabel>Show Furigana</SettingLabel>
              <SettingControl>
                <Toggle 
                  checked={settings.showFurigana} 
                  onClick={() => handleUpdateSetting('showFurigana', !settings.showFurigana)}
                />
              </SettingControl>
            </SettingRow>
            
            {/* Replace dictionary settings with new component */}
            <DictionaryManagement />
          </>
        )}
        
        <SettingRow>
          <SettingLabel>Difficulty</SettingLabel>
          <SettingControl>
            <Select 
              value={settings.difficulty} 
              onChange={(e) => handleUpdateSetting('difficulty', e.target.value)}
            >
              <option value="easy">Easy</option>
              <option value="medium">Medium</option>
              <option value="hard">Hard</option>
            </Select>
          </SettingControl>
        </SettingRow>
        
        <SettingRow>
          <SettingLabel>Card Types to Study</SettingLabel>
          <SettingControl>
            <Select 
              value={settings.cardType} 
              onChange={(e) => handleUpdateSetting('cardType', e.target.value)}
            >
              <option value="all">All Types</option>
              <option value="classic">Anki-style Only</option>
              <option value="multiple">Multiple Choice Only</option>
              <option value="fill">Fill in the Blank Only</option>
            </Select>
          </SettingControl>
        </SettingRow>
        
        <SettingRow>
          <SettingLabel>Reviews Per Day</SettingLabel>
          <SettingControl>
            <RangeSlider
              type="range"
              min="5"
              max="50"
              step="5"
              value={settings.reviewsPerDay}
              onChange={(e) => handleUpdateSetting('reviewsPerDay', parseInt(e.target.value))}
            />
            <RangeValue>{settings.reviewsPerDay}</RangeValue>
          </SettingControl>
        </SettingRow>

        <SettingRow>
          <SettingLabel>Card Navigation</SettingLabel>
          <SettingControl>
            <Select 
              value={settings.cardAdvanceMode} 
              onChange={(e) => handleUpdateSetting('cardAdvanceMode', e.target.value)}
            >
              <option value="manual">Manual (Always click to continue)</option>
              <option value="autoCorrect">Semi-Auto (Auto on correct answers only)</option>
              <option value="auto">Auto (Auto proceed on all answers)</option>
            </Select>
          </SettingControl>
        </SettingRow>
        
      </SettingsGroup>
      
      <SectionTitle>Spaced Repetition</SectionTitle>
      <SettingsGroup>
        <SettingRow>
          <SettingLabel>Easy Interval (days)</SettingLabel>
          <SettingControl>
            <NumberInput
              type="number"
              min="1"
              max="10"
              value={settings.spacedRepetition.easyInterval}
              onChange={(e) => handleUpdateSpacedRepetition('easyInterval', parseInt(e.target.value))}
            />
          </SettingControl>
        </SettingRow>
        
        <SettingRow>
          <SettingLabel>Medium Interval (days)</SettingLabel>
          <SettingControl>
            <NumberInput
              type="number"
              min="1"
              max="5"
              value={settings.spacedRepetition.mediumInterval}
              onChange={(e) => handleUpdateSpacedRepetition('mediumInterval', parseInt(e.target.value))}
            />
          </SettingControl>
        </SettingRow>
        
        <SettingRow>
          <SettingLabel>Hard Interval (days)</SettingLabel>
          <SettingControl>
            <NumberInput
              type="number"
              min="1"
              max="3"
              value={settings.spacedRepetition.hardInterval}
              onChange={(e) => handleUpdateSpacedRepetition('hardInterval', parseInt(e.target.value))}
            />
          </SettingControl>
        </SettingRow>
      </SettingsGroup>
      
      <SectionTitle>OpenAI Integration</SectionTitle>
      <SettingsGroup>
        <SettingRow>
          <SettingLabel>API Key</SettingLabel>
          <SettingControl>
            <FormGroup>
              <label>
                <span>API Key:</span>
                
                {apiKeyVisible ? (
                  <ApiKeyInput>
                    <input
                      type="text"
                      value={tempApiKey || ''}
                      onChange={(e) => setTempApiKey(e.target.value)}
                      placeholder="Enter your OpenAI API key"
                    />
                    <ApiKeyButtons>
                      <ApiKeyButton 
                        onClick={handleSaveApiKey}
                        disabled={!tempApiKey}
                      >
                        Save
                      </ApiKeyButton>
                      <ApiKeyButton 
                        onClick={handleTestApiKey} 
                        disabled={isValidating || !tempApiKey}
                      >
                        {isValidating ? 'Testing...' : 'Test Key'}
                      </ApiKeyButton>
                    
                    </ApiKeyButtons>
                    
                    {apiKeyValidation && (
                      <ValidationMessage valid={apiKeyValidation.valid}>
                        {apiKeyValidation.valid 
                          ? (apiKeyValidation.message || '✓ API key is valid!') 
                          : `✗ API key error: ${apiKeyValidation.error}`
                        }
                      </ValidationMessage>
                    )}
                  </ApiKeyInput>
                ) : (
                  <ApiKeyDisplay>
                    <ApiKeyMasked>
                      {settings.apiKey ? '••••••••••••••••' : 'No API key set'}
                    </ApiKeyMasked>
                    <ApiKeyButtonGroup>
                      <ApiKeyButton onClick={() => setApiKeyVisible(true)}>
                        {settings.apiKey ? 'Change' : 'Add API Key'}
                      </ApiKeyButton>
                    </ApiKeyButtonGroup>
                  </ApiKeyDisplay>
                )}
                
                {/* Always show validation message if present */}
                {!apiKeyVisible && apiKeyValidation && (
                  <ValidationMessage valid={apiKeyValidation.valid}>
                    {apiKeyValidation.valid 
                      ? (apiKeyValidation.message || '✓ API key is valid!')
                      : `✗ API key error: ${apiKeyValidation.error}`
                    }
                  </ValidationMessage>
                )}
              </label>
            </FormGroup>
          </SettingControl>
        </SettingRow>
      </SettingsGroup>
      
      <InfoBox>
        <p>To generate flashcards with AI, you need to provide your OpenAI API key. Your key is stored locally on your device.</p>
        <p>You can get an API key from <a href="https://platform.openai.com/account/api-keys" target="_blank" rel="noopener noreferrer">OpenAI's website</a>.</p>
      </InfoBox>
      
      <ResetSection>
        <DangerButton onClick={() => {
          if (window.confirm('Are you sure you want to reset all settings to default? This cannot be undone.')) {
            dispatch(updateSettings({ 
              theme: 'light',
              appLanguage: 'en',
              learningLanguage: 'es',
              difficulty: 'medium',
              cardType: 'all',
              reviewsPerDay: 20,
              soundEffects: true,
              notifications: true,
            }));
          }
        }}>
          Reset to Default Settings
        </DangerButton>
      </ResetSection>
    </Container>
  );
};

const Container = styled.div`
  padding: 16px;
  max-width: 600px;
  margin: 0 auto;
`;

const PageTitle = styled.h1`
  font-size: 24px;
  margin-bottom: 24px;
  text-align: center;
  color: ${props => props.theme.text};
`;

const SectionTitle = styled.h2`
  font-size: 18px;
  margin: 24px 0 12px;
  color: ${props => props.theme.text};
  border-bottom: 1px solid ${props => props.theme.border};
  padding-bottom: 8px;
`;

const SettingsGroup = styled.div`
  background: ${props => props.theme.backgroundLight};
  border-radius: 8px;
  overflow: hidden;
`;

const SettingRow = styled.div`
  display: flex;
  padding: 12px 16px;
  border-bottom: 1px solid ${props => props.theme.border};
  
  &:last-child {
    border-bottom: none;
  }
`;

const SettingLabel = styled.div`
  flex: 1;
  display: flex;
  align-items: center;
  font-size: 16px;
`;

const SettingControl = styled.div`
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: flex-end;
`;

// Add the missing FormGroup component
const FormGroup = styled.div`
  width: 100%;
  
  label {
    display: flex;
    flex-direction: column;
    gap: 8px;
  }
  
  span {
    font-weight: 500;
  }
`;

const Select = styled.select`
  padding: 8px;
  border: 1px solid ${props => props.theme.border};
  border-radius: 4px;
  background: ${props => props.theme.background};
  color: ${props => props.theme.text};
  min-width: 150px;
`;

// Replace the current Toggle component
const Toggle = styled.button`
  width: 50px;
  height: 24px;
  border-radius: 12px;
  background: ${props => props.checked ? props.theme.primary : props.theme.border};
  position: relative;
  cursor: pointer;
  transition: background 0.2s;
  border: none;
  padding: 0;
  outline: none;
  
  &::before {
    content: '';
    position: absolute;
    left: ${props => props.checked ? '26px' : '2px'};
    top: 2px;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background: white;
    transition: left 0.2s;
  }
`;

const RangeSlider = styled.input`
  width: 100%;
  max-width: 200px;
  margin-right: 10px;
  -webkit-appearance: none;
  height: 6px;
  border-radius: 3px;
  background: ${props => props.theme.border};
  outline: none;
  
  &::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 18px;
    height: 18px;
    border-radius: 50%;
    background: ${props => props.theme.primary};
    cursor: pointer;
  }
  
  &::-moz-range-thumb {
    width: 18px;
    height: 18px;
    border-radius: 50%;
    background: ${props => props.theme.primary};
    cursor: pointer;
  }
`;

const RangeValue = styled.span`
  width: 30px;
  text-align: right;
`;

const NumberInput = styled.input`
  width: 60px;
  padding: 8px;
  border: 1px solid ${props => props.theme.border};
  border-radius: 4px;
  text-align: center;
`;

const ApiKeyInput = styled.div`
  display: flex;
  flex-direction: column;
  gap: 8px;
  width: 100%;
  
  input {
    padding: 8px;
    border: 1px solid ${props => props.theme.border};
    border-radius: 4px;
    width: 100%;
  }
`;

const ApiKeyButtons = styled.div`
  display: flex;
  gap: 8px;
`;

const ApiKeyButton = styled.button`
  padding: 8px 12px;
  background: ${props => props.secondary ? 'transparent' : props.theme.primary};
  color: ${props => props.secondary ? props.theme.text : 'white'};
  border: ${props => props.secondary ? `1px solid ${props.theme.border}` : 'none'};
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
`;

const ApiKeyDisplay = styled.div`
  display: flex;
  flex-direction: column;
  gap: 12px;
`;

const ApiKeyMasked = styled.div`
  font-family: monospace;
  color: ${props => props.theme.textLight};
`;

const InfoBox = styled.div`
  padding: 16px;
  background: ${props => props.theme.backgroundLight};
  border-left: 4px solid ${props => props.theme.info};
  margin: 24px 0;
  border-radius: 4px;
  
  p {
    margin: 0 0 8px;
    font-size: 14px;
    
    &:last-child {
      margin-bottom: 0;
    }
  }
  
  a {
    color: ${props => props.theme.primary};
    text-decoration: none;
    
    &:hover {
      text-decoration: underline;
    }
  }
`;

const ResetSection = styled.div`
  margin-top: 32px;
  text-align: center;
`;

const DangerButton = styled.button`
  padding: 10px 16px;
  background: transparent;
  color: ${props => props.theme.error};
  border: 1px solid ${props => props.theme.error};
  border-radius: 4px;
  font-size: 14px;
  cursor: pointer;
  transition: all 0.2s;
  
  &:hover {
    background: ${props => props.theme.error};
    color: white;
  }
`;

const ValidationMessage = styled.div`
  margin-top: 10px;
  padding: 8px 12px;
  border-radius: 4px;
  font-size: 14px;
  background-color: ${props => props.valid 
    ? `${props.theme.success}15` 
    : `${props.theme.error}15`
  };
  color: ${props => props.valid 
    ? props.theme.success 
    : props.theme.error
  };
  border-left: 3px solid ${props => props.valid 
    ? props.theme.success 
    : props.theme.error
  };
`;

const DictionaryButton = styled.button`
  padding: 8px 16px;
  background: ${props => props.theme.accent};
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
`;

const DictionaryProgress = styled.div`
  width: 100%;
  max-width: 200px;
`;

const ProgressBar = styled.div`
  height: 6px;
  background: ${props => props.theme.backgroundDark};
  border-radius: 3px;
  overflow: hidden;
  margin-bottom: 6px;
`;

const ProgressFill = styled.div`
  height: 100%;
  width: ${props => props.width || 0}%;
  background: ${props => props.theme.primary};
  transition: width 0.3s ease;
`;

const ProgressText = styled.div`
  font-size: 12px;
  color: ${props => props.theme.textLight};
  margin-top: 4px;
`;

const DictionaryStatus = styled.div`
  color: ${props => props.theme.success};
  font-size: 14px;
`;

const ApiKeyButtonGroup = styled.div`
  display: flex;
  gap: 8px;
`;

export default SettingsPage;
