import React, { useState } from 'react';
import styled from 'styled-components';
import { debugApiRequest } from '../../services/aiService';

const ApiResponseDebugger = ({ enabled = false, word = '', learningLanguage = 'es', mode = 'single' }) => {
  const [apiResponse, setApiResponse] = useState(null);
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState(null);

  // Only show when debugging is enabled
  if (!enabled) return null;

  const handleTestRequest = async () => {
    if (!word.trim()) {
      setError('Please enter a word or topic to test');
      return;
    }

    setIsLoading(true);
    setError(null);

    try {
      const responseData = await debugApiRequest(word, mode);
      
      if (responseData.error) {
        setError(`API Error: ${responseData.error}`);
        return;
      }
      
      const content = responseData.choices?.[0]?.message?.content;
      
      setApiResponse({
        rawResponse: responseData,
        content: content,
        parseable: isParseable(content)
      });
    } catch (err) {
      setError(`Error: ${err.message}`);
    } finally {
      setIsLoading(false);
    }
  };

  const isParseable = (jsonString) => {
    try {
      JSON.parse(jsonString);
      return true;
    } catch (e) {
      return false;
    }
  };

  return (
    <DebuggerContainer>
      <DebuggerTitle>API Response Debugger {mode === 'batch' ? '(Batch Mode)' : '(Single Mode)'}</DebuggerTitle>
      <DebugButton onClick={handleTestRequest} disabled={isLoading}>
        {isLoading ? 'Testing...' : 'Test API Response'}
      </DebugButton>
      
      {error && <ErrorMessage>{error}</ErrorMessage>}
      
      {apiResponse && (
        <ResponseSection>
          <SectionTitle>Raw Content:</SectionTitle>
          <CodeBlock>{apiResponse.content || 'No content'}</CodeBlock>
          
          <SectionTitle>Parseable as JSON: {apiResponse.parseable ? '✓' : '✗'}</SectionTitle>
          
          {apiResponse.parseable ? (
            <JsonViewer data={JSON.parse(apiResponse.content)} />
          ) : (
            <ExtractedSection>
              <SectionTitle>Attempted JSON extraction:</SectionTitle>
              <CodeBlock>
                {(() => {
                  try {
                    const match = apiResponse.content.match(/(\{.*\})/s);
                    return match ? match[0] : 'No JSON object found';
                  } catch (e) {
                    return 'Error extracting JSON';
                  }
                })()}
              </CodeBlock>
            </ExtractedSection>
          )}
          
          <SectionTitle>Full Response Object:</SectionTitle>
          <CodeBlock>{JSON.stringify(apiResponse.rawResponse, null, 2)}</CodeBlock>
        </ResponseSection>
      )}
    </DebuggerContainer>
  );
};

const DebuggerContainer = styled.div`
  background: ${props => props.theme.backgroundLight};
  border: 1px solid ${props => props.theme.border};
  border-radius: 8px;
  padding: 16px;
  margin-top: 20px;
  font-family: monospace;
`;

const DebuggerTitle = styled.h3`
  margin-top: 0;
  color: ${props => props.theme.text};
  border-bottom: 1px solid ${props => props.theme.border};
  padding-bottom: 8px;
`;

const DebugButton = styled.button`
  background: ${props => props.theme.primaryDark};
  color: ${props => props.theme.buttonText};
  border: none;
  padding: 8px 16px;
  border-radius: 4px;
  cursor: pointer;
  margin-bottom: 16px;
  
  &:disabled {
    background: ${props => props.theme.textLight};
  }
`;

const ResponseSection = styled.div`
  margin-top: 16px;
`;

const SectionTitle = styled.h4`
  margin: 12px 0 4px 0;
  color: ${props => props.theme.text};
`;

const CodeBlock = styled.pre`
  background: ${props => props.theme.backgroundDark};
  color: ${props => props.theme.text};
  padding: 12px;
  border-radius: 4px;
  overflow: auto;
  max-height: 200px;
  font-size: 12px;
`;

const ErrorMessage = styled.div`
  color: ${props => props.theme.error};
  background: ${props => `${props.theme.error}15`};
  padding: 12px;
  border-radius: 4px;
  margin-bottom: 16px;
`;

const ExtractedSection = styled.div`
  margin-top: 10px;
  padding: 8px;
  background: ${props => `${props.theme.accent}15`};
  border-left: 4px solid ${props => props.theme.accent};
  border-radius: 4px;
`;

const JsonViewer = ({ data }) => (
  <CodeBlock>{JSON.stringify(data, null, 2)}</CodeBlock>
);

export default ApiResponseDebugger;
