import { createSlice } from '@reduxjs/toolkit';
import { loadFromLocalStorage, saveToLocalStorage } from '../../services/localStorageService';

const initialState = loadFromLocalStorage('character') || {
  hair: 'style1',
  eyes: 'style1',
  mouth: 'style1',
  clothes: 'style1',
  background: 'default',
  unlockedItems: {
    hair: ['style1'],
    eyes: ['style1'],
    mouth: ['style1'],
    clothes: ['style1'],
    background: ['default']
  }
};

const characterSlice = createSlice({
  name: 'character',
  initialState,
  reducers: {
    updateCharacter: (state, action) => {
      const { feature, value } = action.payload;
      if (state.unlockedItems[feature].includes(value)) {
        state[feature] = value;
        saveToLocalStorage('character', state);
      }
    },
    unlockItem: (state, action) => {
      const { feature, value } = action.payload;
      if (!state.unlockedItems[feature].includes(value)) {
        state.unlockedItems[feature].push(value);
        saveToLocalStorage('character', state);
      }
    }
  },
});

export const { updateCharacter, unlockItem } = characterSlice.actions;
export default characterSlice.reducer;
