import React from 'react';
import styled from 'styled-components';

class ErrorBoundary extends React.Component {
  constructor(props) {
    super(props);
    this.state = { 
      hasError: false,
      error: null,
      errorInfo: null
    };
  }

  static getDerivedStateFromError(error) {
    // Update state so the next render shows the fallback UI
    return { hasError: true, error };
  }

  componentDidCatch(error, errorInfo) {
    // Log the error to the console
    console.error('Error caught by ErrorBoundary:', error, errorInfo);
    this.setState({
      error,
      errorInfo
    });
  }

  handleRetry = () => {
    // Reset the error state to allow another attempt
    this.setState({
      hasError: false,
      error: null,
      errorInfo: null
    });
    
    // Call the onRetry prop if provided
    if (this.props.onRetry) {
      this.props.onRetry();
    }
  }

  render() {
    if (this.state.hasError) {
      // Render fallback UI when an error occurs
      return (
        <ErrorContainer>
          <ErrorHeader>An error occurred</ErrorHeader>
          <ErrorMessage>
            {this.state.error && this.state.error.toString()}
          </ErrorMessage>
          <ErrorActions>
            <RetryButton onClick={this.handleRetry}>Try Again</RetryButton>
          </ErrorActions>
        </ErrorContainer>
      );
    }

    return this.props.children;
  }
}

const ErrorContainer = styled.div`
  padding: 20px;
  background: ${props => `${props.theme.error}15`};
  border-radius: 8px;
  border-left: 4px solid ${props => props.theme.error};
  margin: 20px 0;
`;

const ErrorHeader = styled.h3`
  color: ${props => props.theme.error};
  margin-top: 0;
  margin-bottom: 10px;
`;

const ErrorMessage = styled.pre`
  font-family: monospace;
  white-space: pre-wrap;
  word-break: break-word;
  background: rgba(0,0,0,0.05);
  padding: 10px;
  border-radius: 4px;
  font-size: 12px;
  max-height: 200px;
  overflow-y: auto;
`;

const ErrorActions = styled.div`
  margin-top: 15px;
  text-align: right;
`;

const RetryButton = styled.button`
  padding: 8px 16px;
  background: ${props => props.theme.primary};
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  
  &:hover {
    background: ${props => props.theme.primaryDark};
  }
`;

export default ErrorBoundary;
