import React, { useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import styled from 'styled-components';
import { updateCharacter } from '../redux/slices/characterSlice';
import Character from '../components/Character/Character';

const CharacterPage = () => {
  const dispatch = useDispatch();
  const { currentLevel, streak } = useSelector(state => state.study);
  const character = useSelector(state => state.character);
  const [activeTab, setActiveTab] = useState('hair');
  
  const handleUpdateCharacter = (feature, value) => {
    dispatch(updateCharacter({ feature, value }));
  };
  
  const renderFeatureOptions = (feature) => {
    const allOptions = getFeatureOptions(feature);
    const unlockedOptions = character.unlockedItems[feature];
    
    return (
      <OptionsGrid>
        {allOptions.map(option => {
          const isUnlocked = unlockedOptions.includes(option.id);
          const isSelected = character[feature] === option.id;
          
          return (
            <OptionItem 
              key={option.id}
              unlocked={isUnlocked}
              selected={isSelected}
              onClick={() => isUnlocked && handleUpdateCharacter(feature, option.id)}
            >
              <OptionImage src={option.image} alt={option.name} />
              <OptionName>{option.name}</OptionName>
              {!isUnlocked && (
                <LockOverlay>
                  <LockIcon>🔒</LockIcon>
                  <LockText>
                    {option.unlockLevel ? `Level ${option.unlockLevel}` : 
                     option.unlockStreak ? `${option.unlockStreak} day streak` : 
                     'Locked'}
                  </LockText>
                </LockOverlay>
              )}
            </OptionItem>
          );
        })}
      </OptionsGrid>
    );
  };
  
  // This would normally fetch real image paths
  const getFeatureOptions = (feature) => {
    switch (feature) {
      case 'hair':
        return [
          { id: 'style1', name: 'Default', image: '/assets/character/hair/style1.png', unlockLevel: 1 },
          { id: 'style2', name: 'Curly', image: '/assets/character/hair/style2.png', unlockLevel: 2 },
          { id: 'style3', name: 'Long', image: '/assets/character/hair/style3.png', unlockLevel: 3 },
          { id: 'style4', name: 'Short', image: '/assets/character/hair/style4.png', unlockStreak: 3 }
        ];
      case 'eyes':
        return [
          { id: 'style1', name: 'Default', image: '/assets/character/eyes/style1.png', unlockLevel: 1 },
          { id: 'style2', name: 'Happy', image: '/assets/character/eyes/style2.png', unlockLevel: 2 },
          { id: 'style3', name: 'Cool', image: '/assets/character/eyes/style3.png', unlockStreak: 5 }
        ];
      case 'mouth':
        return [
          { id: 'style1', name: 'Default', image: '/assets/character/mouth/style1.png', unlockLevel: 1 },
          { id: 'style2', name: 'Smile', image: '/assets/character/mouth/style2.png', unlockLevel: 2 },
          { id: 'style3', name: 'Surprised', image: '/assets/character/mouth/style3.png', unlockStreak: 4 }
        ];
      case 'clothes':
        return [
          { id: 'style1', name: 'Default', image: '/assets/character/clothes/style1.png', unlockLevel: 1 },
          { id: 'style2', name: 'Formal', image: '/assets/character/clothes/style2.png', unlockLevel: 3 },
          { id: 'style3', name: 'Casual', image: '/assets/character/clothes/style3.png', unlockLevel: 4 },
          { id: 'style4', name: 'Sport', image: '/assets/character/clothes/style4.png', unlockStreak: 7 }
        ];
      case 'background':
        return [
          { id: 'default', name: 'Default', image: '/assets/character/background/default.png', unlockLevel: 1 },
          { id: 'beach', name: 'Beach', image: '/assets/character/background/beach.png', unlockLevel: 3 },
          { id: 'city', name: 'City', image: '/assets/character/background/city.png', unlockLevel: 5 },
          { id: 'space', name: 'Space', image: '/assets/character/background/space.png', unlockStreak: 10 }
        ];
      default:
        return [];
    }
  };

  return (
    <Container>
      <PageTitle>Character Customization</PageTitle>
      
      <CharacterPreview>
        <Character character={character} />
      </CharacterPreview>
      
      <StatsInfo>
        <StatBadge>Level {currentLevel}</StatBadge>
        <StatBadge>{streak} Day Streak</StatBadge>
      </StatsInfo>
      
      <DisabledOverlay>
        <LockMessage>
          <OverlayLockIcon>🔒</OverlayLockIcon>
          Character customization is not implemented yet, sorry :c
        </LockMessage>
      </DisabledOverlay>
      
      <FeatureTabs style={{ opacity: 0.5, pointerEvents: 'none' }}>
        {['hair', 'eyes', 'mouth', 'clothes', 'background'].map(feature => (
          <FeatureTab 
            key={feature} 
            active={activeTab === feature}
            onClick={() => setActiveTab(feature)}
          >
            {feature.charAt(0).toUpperCase() + feature.slice(1)}
          </FeatureTab>
        ))}
      </FeatureTabs>
      
      <OptionsContainer style={{ opacity: 0.5, pointerEvents: 'none' }}>
        {renderFeatureOptions(activeTab)}
      </OptionsContainer>
      
      <InfoBox>
        <p>Character customization is currently undergoing maintenance.</p>
        <p>Unlock new customization options by leveling up and maintaining your streak!</p>
      </InfoBox>
    </Container>
  );
};

const Container = styled.div`
  padding: 16px;
  max-width: 600px;
  margin: 0 auto;
`;

const PageTitle = styled.h1`
  font-size: 24px;
  margin-bottom: 24px;
  text-align: center;
  color: ${props => props.theme.text};
`;

const CharacterPreview = styled.div`
  background: ${props => props.theme.backgroundLight};
  border-radius: 8px;
  padding: 32px;
  margin-bottom: 24px;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 200px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
`;

const StatsInfo = styled.div`
  display: flex;
  justify-content: center;
  gap: 16px;
  margin-bottom: 24px;
`;

const StatBadge = styled.div`
  background: ${props => props.theme.primary};
  color: white;
  padding: 6px 12px;
  border-radius: 16px;
  font-weight: 500;
  font-size: 14px;
`;

const FeatureTabs = styled.div`
  display: flex;
  margin-bottom: 16px;
  overflow-x: auto;
  gap: 2px;
  border-radius: 8px;
  background: ${props => props.theme.backgroundDark};
  padding: 4px;
`;

const FeatureTab = styled.button`
  padding: 10px;
  background: ${props => props.active ? props.theme.primary : 'transparent'};
  color: ${props => props.active ? 'white' : props.theme.text};
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: all 0.2s;
  flex: 1;
  min-width: 80px;
  font-weight: ${props => props.active ? '600' : '400'};
  
  &:hover {
    background: ${props => props.active ? props.theme.primary : props.theme.backgroundLight};
  }
`;

const OptionsContainer = styled.div`
  margin-bottom: 20px;
  background: ${props => props.theme.backgroundLight};
  border-radius: 8px;
  padding: 16px;
`;

const OptionsGrid = styled.div`
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(120px, 1fr));
  gap: 16px;
`;

const OptionItem = styled.div`
  border-radius: 8px;
  border: 2px solid ${props => props.selected ? props.theme.primary : 'transparent'};
  overflow: hidden;
  position: relative;
  cursor: ${props => props.unlocked ? 'pointer' : 'not-allowed'};
  transition: transform 0.2s, border 0.2s;
  background: ${props => props.theme.background};
  
  &:hover {
    transform: ${props => props.unlocked ? 'translateY(-5px)' : 'none'};
  }
`;

const OptionImage = styled.img`
  width: 100%;
  height: 100px;
  object-fit: cover;
  display: block;
`;

const OptionName = styled.div`
  padding: 8px;
  font-size: 14px;
  text-align: center;
`;

const LockOverlay = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
`;

const LockIcon = styled.div`
  font-size: 24px;
  margin-bottom: 8px;
`;

const LockText = styled.div`
  font-size: 12px;
  padding: 0 8px;
  text-align: center;
`;

const InfoBox = styled.div`
  padding: 12px;
  background: ${props => props.theme.backgroundLight};
  border-radius: 8px;
  margin-top: 16px;
  border-left: 4px solid ${props => props.theme.info};
  
  p {
    margin: 0;
    font-size: 14px;
    color: ${props => props.theme.text};
  }
`;

const DisabledOverlay = styled.div`
  position: relative;
  padding: 20px;
  margin: 20px 0;
  text-align: center;
  background: ${props => props.theme.backgroundLight};
  border-radius: 8px;
`;

const LockMessage = styled.div`
  font-size: 18px;
  color: ${props => props.theme.textLight};
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
`;

// Rename this to avoid duplication
const OverlayLockIcon = styled.span`
  font-size: 32px;
`;

export default CharacterPage;
