import React from 'react';
import styled from 'styled-components';

// This is a simplified character component
// In a real app, you would use SVG or images to create the character
const Character = ({ character }) => {
  return (
    <CharacterContainer background={character.background}>
      <CharacterFigure>
        {/* Placeholder for actual character display */}
        <CharacterHead>
          <CharacterHair data-style={character.hair} />
          <CharacterFace>
            <CharacterEyes data-style={character.eyes} />
            <CharacterMouth data-style={character.mouth} />
          </CharacterFace>
        </CharacterHead>
        <CharacterBody>
          <CharacterClothes data-style={character.clothes} />
        </CharacterBody>
      </CharacterFigure>
    </CharacterContainer>
  );
};

const CharacterContainer = styled.div`
  width: 220px;
  height: 300px;
  position: relative;
  border-radius: 8px;
  overflow: hidden;
  background: ${props => {
    switch (props.background) {
      case 'beach': return 'linear-gradient(to bottom, #87CEEB, #E0F7FA)';
      case 'city': return 'linear-gradient(to bottom, #3F51B5, #9E9E9E)';
      case 'space': return 'linear-gradient(to bottom, #000000, #311B92)';
      default: return 'linear-gradient(to bottom, #B3E5FC, #E1F5FE)';
    }
  }};
`;

const CharacterFigure = styled.div`
  position: absolute;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  flex-direction: column;
  align-items: center;
`;

const CharacterHead = styled.div`
  width: 80px;
  height: 100px;
  border-radius: 50%;
  background: #FFD3B6;
  position: relative;
  z-index: 2;
`;

const CharacterHair = styled.div`
  position: absolute;
  top: -10px;
  left: 0;
  right: 0;
  height: 40px;
  
  &[data-style="style1"] {
    background: #8D6E63;
    border-radius: 40px 40px 0 0;
  }
  
  &[data-style="style2"] {
    background: #6D4C41;
    border-radius: 40px 40px 0 0;
    &::after {
      content: '';
      position: absolute;
      top: 5px;
      left: 5px;
      right: 5px;
      height: 10px;
      background: #8D6E63;
      border-radius: 20px;
    }
  }
  
  &[data-style="style3"] {
    background: #5D4037;
    height: 60px;
    border-radius: 40px 40px 0 0;
  }
  
  &[data-style="style4"] {
    background: #4E342E;
    height: 25px;
    border-radius: 40px 40px 0 0;
  }
`;

const CharacterFace = styled.div`
  position: absolute;
  top: 30px;
  left: 0;
  right: 0;
  height: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding: 10px 0;
`;

const CharacterEyes = styled.div`
  width: 60px;
  height: 10px;
  display: flex;
  justify-content: space-between;
  
  &[data-style="style1"]::before, 
  &[data-style="style1"]::after {
    content: '';
    width: 10px;
    height: 10px;
    background: #424242;
    border-radius: 50%;
  }
  
  &[data-style="style2"] {
    &::before, &::after {
      content: '';
      width: 10px;
      height: 5px;
      background: #424242;
      border-radius: 10px 10px 0 0;
    }
  }
  
  &[data-style="style3"] {
    &::before, &::after {
      content: '';
      width: 12px;
      height: 8px;
      background: #424242;
      border-radius: 4px;
    }
  }
`;

const CharacterMouth = styled.div`
  width: 30px;
  height: 10px;
  
  &[data-style="style1"] {
    background: #F44336;
    height: 3px;
    border-radius: 2px;
  }
  
  &[data-style="style2"] {
    background: #F44336;
    border-radius: 0 0 15px 15px;
  }
  
  &[data-style="style3"] {
    background: #F44336;
    border-radius: 50%;
  }
`;

const CharacterBody = styled.div`
  width: 100px;
  height: 120px;
  background: #FFD3B6;
  border-radius: 20px 20px 0 0;
  position: relative;
  margin-top: -20px;
`;

const CharacterClothes = styled.div`
  position: absolute;
  top: 10px;
  left: 0;
  right: 0;
  bottom: 0;
  
  &[data-style="style1"] {
    background: #2196F3;
    border-radius: 15px 15px 0 0;
  }
  
  &[data-style="style2"] {
    background: #212121;
    border-radius: 15px 15px 0 0;
    &::after {
      content: '';
      position: absolute;
      top: 20px;
      left: 35px;
      width: 30px;
      height: 5px;
      background: white;
    }
  }
  
  &[data-style="style3"] {
    background: #4CAF50;
    border-radius: 15px 15px 0 0;
  }
  
  &[data-style="style4"] {
    background: #FF9800;
    border-radius: 15px 15px 0 0;
    &::after {
      content: '';
      position: absolute;
      top: 15px;
      left: 25px;
      width: 50px;
      height: 20px;
      border-radius: 25px;
      background: #FFC107;
    }
  }
`;

export default Character;
