import React from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
import styled from 'styled-components';
import { useSelector } from 'react-redux';

const Header = () => {
  const navigate = useNavigate();
  const location = useLocation();
  const { streak } = useSelector(state => state.study);
  
  const getPageTitle = () => {
    const path = location.pathname;
    
    if (path === '/') return 'Home';
    if (path === '/flashcards') return 'Flashcards';
    if (path === '/study') return 'Study';
    if (path.startsWith('/study/session')) return 'Study Session';
    if (path === '/character') return 'Character';
    if (path === '/settings') return 'Settings';
    
    return 'Paraleet';
  };

  return (
    <HeaderContainer>
      <LogoSection onClick={() => navigate('/')}>
        <Logo>🦜</Logo>
        <AppName>Paraleet</AppName>
      </LogoSection>
      
      <TitleSection>
        <PageTitle>{getPageTitle()}</PageTitle>
      </TitleSection>
      
      <StatsSection>
        {streak > 0 && (
          <StreakBadge>
            <StreakIcon>🔥</StreakIcon>
            <StreakCount>{streak}</StreakCount>
          </StreakBadge>
        )}
      </StatsSection>
    </HeaderContainer>
  );
};

const HeaderContainer = styled.header`
  display: flex;
  align-items: center;
  padding: 16px;
  background: ${props => props.theme.backgroundLight};
  border-bottom: 1px solid ${props => props.theme.border};
  position: sticky;
  top: 0;
  z-index: 10;
`;

const LogoSection = styled.div`
  display: flex;
  align-items: center;
  cursor: pointer;
`;

const Logo = styled.div`
  font-size: 24px;
  margin-right: 8px;
`;

const AppName = styled.h1`
  font-size: 20px;
  margin: 0;
  color: ${props => props.theme.primary};
  font-weight: 700;
`;

const TitleSection = styled.div`
  flex: 1;
  text-align: center;
`;

const PageTitle = styled.h2`
  font-size: 18px;
  margin: 0;
  color: ${props => props.theme.text};
`;

const StatsSection = styled.div`
  display: flex;
  align-items: center;
`;

const StreakBadge = styled.div`
  display: flex;
  align-items: center;
  background: ${props => props.theme.accent};
  color: ${props => props.theme.buttonText};
  padding: 4px 10px;
  border-radius: 16px;
`;

const StreakIcon = styled.span`
  margin-right: 4px;
`;

const StreakCount = styled.span`
  font-weight: 600;
`;

export default Header;
