import React, { useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import styled from 'styled-components';
import FlashcardCreator from '../components/Flashcard/FlashcardCreator';
import AssistedFlashcardCreator from '../components/Flashcard/AssistedFlashcardCreator';
import FlashcardList from '../components/Flashcard/FlashcardList';
import ApiResponseDebugger from '../components/Debug/ApiResponseDebugger';
import { generateFlashcards, generateFlashcardsInBatches } from '../services/aiService';
import { addFlashcard, addCategory } from '../redux/slices/flashcardsSlice';
import MyGroups from '../components/Flashcard/MyGroups';
import ErrorBoundary from '../components/Flashcard/ErrorBoundary';

const FlashcardPage = () => {
  const dispatch = useDispatch();
  const [activeTab, setActiveTab] = useState('create');
  const [creationMethod, setCreationMethod] = useState('manual');
  const [isGenerating, setIsGenerating] = useState(false);
  const [generationError, setGenerationError] = useState(null);
  const [errorDetails, setErrorDetails] = useState(null);
  const [generationPrompt, setGenerationPrompt] = useState('');
  const [flashcardCount, setFlashcardCount] = useState(5);
  const [generationCardTypes, setGenerationCardTypes] = useState(['classic', 'multiple', 'fill']);
  const [isDebugging, setIsDebugging] = useState(false);
  // Add state for custom category
  const [generationCategory, setGenerationCategory] = useState('');
  const [isAddingCategory, setIsAddingCategory] = useState(false);
  const [newCategory, setNewCategory] = useState('');
  
  const { cards, categories } = useSelector(state => state.flashcards);
  const { apiKey } = useSelector(state => state.settings);

  // Add new state variables for batch generation
  const [batchProgress, setBatchProgress] = useState(null);
  const [generatedCount, setGeneratedCount] = useState(0);

  const handleGenerateFlashcards = async () => {
    if (!apiKey) {
      setGenerationError('Please add your OpenAI API key in the settings.');
      setErrorDetails(null);
      return;
    }

    if (!generationPrompt.trim()) {
      setGenerationError('Please enter a topic or prompt.');
      setErrorDetails(null);
      return;
    }

    setIsGenerating(true);
    setGenerationError(null);
    setErrorDetails(null);
    setIsDebugging(false);
    setBatchProgress(null);
    setGeneratedCount(0);

    try {
      // Add the category first if it's new
      const categoryToUse = generationCategory || generationPrompt;
      if (categoryToUse && !categories.includes(categoryToUse)) {
        dispatch(addCategory(categoryToUse));
      }
      
      // For smaller requests (10 or fewer cards), use regular generation
      if (flashcardCount <= 10) {
        const generatedCards = await generateFlashcards(
          generationPrompt, 
          flashcardCount,
          generationCardTypes
        );
        
        generatedCards.forEach(card => {
          // Don't use the generic "Translation of:" placeholder - ensure proper translation
          // Only use placeholder if the example exists but translation is completely missing
          if (card.sentenceExample && (!card.sentenceExampleTranslation || 
              card.sentenceExampleTranslation.startsWith('Translation of:'))) {
            console.log(`Requesting translation for example: ${card.sentenceExample}`);
            // We'll have the AI service handle this with a proper translation now
          }
          
          dispatch(addFlashcard({
            ...card,
            category: categoryToUse,
          }));
        });
      } 
      // For larger requests, use the batched generation
      else {
        const generatedCards = await generateFlashcardsInBatches(
          generationPrompt,
          flashcardCount,
          generationCardTypes,
          (progress) => {
            setBatchProgress(progress);
            setGeneratedCount(progress.cardsGenerated || 0);
          }
        );
        
        generatedCards.forEach(card => {
          // Same improvement as above - avoid generic placeholders
          if (card.sentenceExample && (!card.sentenceExampleTranslation || 
              card.sentenceExampleTranslation.startsWith('Translation of:'))) {
            console.log(`Requesting translation for batch example: ${card.sentenceExample}`);
            // We'll have the AI service handle this with a proper translation now
          }
          
          dispatch(addFlashcard({
            ...card,
            category: categoryToUse,
          }));
        });
      }
      
      setGenerationPrompt('');
      setActiveTab('list');
    } catch (error) {
      // Extract error type and message
      const errorParts = error.message.split(': ');
      const errorType = errorParts.length > 1 ? errorParts[0] : 'ERROR';
      const errorMessage = errorParts.length > 1 ? errorParts.slice(1).join(': ') : error.message;
      
      setGenerationError(`Generation Failed: ${errorType}`);
      setErrorDetails(errorMessage);
      console.error(`${errorType}: ${errorMessage}`);
      
      // Enable debugging if it's a JSON parse error
      if (errorType === 'JSON_PARSE_ERROR') {
        setIsDebugging(true);
      }
    } finally {
      setIsGenerating(false);
      setBatchProgress(null);
    }
  };

  const handleAddCategory = () => {
    if (newCategory.trim()) {
      dispatch(addCategory(newCategory.trim()));
      setGenerationCategory(newCategory.trim());
      setNewCategory('');
      setIsAddingCategory(false);
    }
  };

  const renderAutomaticCreator = () => {
    return (
      <AutomaticCreator>
        <GenerationForm>
          <label>
            <span>Topic or prompt:</span>
            <input 
              type="text" 
              value={generationPrompt}
              onChange={(e) => setGenerationPrompt(e.target.value)}
              placeholder="e.g., Basic Spanish greetings"
            />
          </label>
          
          <label>
            <span>Number of flashcards:</span>
            <input 
              type="number" 
              value={flashcardCount}
              onChange={(e) => setFlashcardCount(Math.max(1, Math.min(50, parseInt(e.target.value) || 5)))}
              min="1"
              max="50"
            />
          </label>
          
          <CardTypesLabel>Card types to generate:</CardTypesLabel>
          <CardTypesCheckboxes>
            <CheckboxLabel>
              <input
                type="checkbox"
                checked={generationCardTypes.includes('classic')}
                onChange={(e) => {
                  if (e.target.checked) {
                    setGenerationCardTypes([...generationCardTypes, 'classic']);
                  } else {
                    // Don't uncheck if it's the last option
                    if (generationCardTypes.length > 1) {
                      setGenerationCardTypes(generationCardTypes.filter(t => t !== 'classic'));
                    }
                  }
                }}
              />
              Anki-style
            </CheckboxLabel>
            <CheckboxLabel>
              <input
                type="checkbox"
                checked={generationCardTypes.includes('multiple')}
                onChange={(e) => {
                  if (e.target.checked) {
                    setGenerationCardTypes([...generationCardTypes, 'multiple']);
                  } else {
                    if (generationCardTypes.length > 1) {
                      setGenerationCardTypes(generationCardTypes.filter(t => t !== 'multiple'));
                    }
                  }
                }}
              />
              Multiple Choice
            </CheckboxLabel>
            <CheckboxLabel>
              <input
                type="checkbox"
                checked={generationCardTypes.includes('fill')}
                onChange={(e) => {
                  if (e.target.checked) {
                    setGenerationCardTypes([...generationCardTypes, 'fill']);
                  } else {
                    if (generationCardTypes.length > 1) {
                      setGenerationCardTypes(generationCardTypes.filter(t => t !== 'fill'));
                    }
                  }
                }}
              />
              Fill in the Blank
            </CheckboxLabel>
          </CardTypesCheckboxes>
          
          <label>
            <span>Category:</span>
            <CategorySelector>
              <select 
                value={generationCategory} 
                onChange={(e) => setGenerationCategory(e.target.value)}
              >
                <option value="">Use prompt as category</option>
                {categories.map((cat, index) => (
                  <option key={index} value={cat}>{cat}</option>
                ))}
              </select>
              <AddCategoryButton onClick={() => setIsAddingCategory(true)}>
                + Add Category
              </AddCategoryButton>
            </CategorySelector>
            {isAddingCategory && (
              <CategoryInput>
                <input 
                  type="text"
                  value={newCategory}
                  onChange={(e) => setNewCategory(e.target.value)}
                  placeholder="New category name"
                />
                <ButtonGroup>
                  <AddButton onClick={handleAddCategory}>Add</AddButton>
                  <CancelButton onClick={() => setIsAddingCategory(false)}>Cancel</CancelButton>
                </ButtonGroup>
              </CategoryInput>
            )}
          </label>
          
          {batchProgress && (
            <BatchProgressContainer>
              <BatchProgressHeader>
                Generating cards in batches...
                {batchProgress.cardsGenerated && (
                  <BatchCounter>
                    {batchProgress.cardsGenerated} of {flashcardCount} cards created
                  </BatchCounter>
                )}
              </BatchProgressHeader>
              <ProgressTrack>
                <ProgressFill width={batchProgress.progress || 0} />
              </ProgressTrack>
              <BatchDetails>
                {batchProgress.message}
                {batchProgress.currentBatch && (
                  <span> (Batch {batchProgress.currentBatch}/{batchProgress.totalBatches})</span>
                )}
              </BatchDetails>
            </BatchProgressContainer>
          )}
          
          {generationError && (
            <ErrorContainer>
              <ErrorMessage>{generationError}</ErrorMessage>
              {errorDetails && <ErrorDetails>{errorDetails}</ErrorDetails>}
              <ErrorTip>
                {generationError.includes('API_KEY') && 'Check that your API key is entered correctly in Settings.'}
                {generationError.includes('NETWORK') && 'Check your internet connection and try again.'}
                {generationError.includes('JSON') && 'The AI provided an invalid response format. Try a different prompt.'}
              </ErrorTip>
            </ErrorContainer>
          )}
          
          <GenerateButton 
            onClick={handleGenerateFlashcards}
            disabled={isGenerating || !generationPrompt.trim() || generationCardTypes.length === 0}
          >
            {isGenerating ? (
              flashcardCount > 10 ? `Generating batches... ${generatedCount}/${flashcardCount}` : 'Generating...'
            ) : 'Generate Flashcards'}
          </GenerateButton>
          
          {!apiKey && (
            <NoteText>
              Note: You need to add your OpenAI API key in Settings to use this feature.
            </NoteText>
          )}
        </GenerationForm>
        
        {isDebugging && (
          <ApiResponseDebuggerContainer>
            <ApiResponseDebugger
              enabled={true}
              word={generationPrompt}
              learningLanguage="mixed"
              mode="batch"
            />
          </ApiResponseDebuggerContainer>
        )}
      </AutomaticCreator>
    );
  };

  const renderCreationMethod = () => {
    switch (creationMethod) {
      case 'manual':
        return <FlashcardCreator />;
      case 'assisted':
        return <AssistedFlashcardCreator />;
      case 'automatic':
        return renderAutomaticCreator();
      default:
        return <FlashcardCreator />;
    }
  };

  return (
    <Container>
      <TabContainer>
        <Tab active={activeTab === 'create'} onClick={() => setActiveTab('create')}>
          Create Flashcards
        </Tab>
        <Tab active={activeTab === 'list'} onClick={() => setActiveTab('list')}>
          My Flashcards ({cards.length})
        </Tab>
        <Tab active={activeTab === 'groups'} onClick={() => setActiveTab('groups')}>
          My Groups ({categories.length})
        </Tab>
      </TabContainer>

      <ErrorBoundary>
        {activeTab === 'create' ? (
          <>
            <CreationMethodSelector>
              <MethodButton 
                active={creationMethod === 'manual'}
                onClick={() => setCreationMethod('manual')}
              >
                <MethodIcon>✍️</MethodIcon>
                <MethodTitle>Manual</MethodTitle>
                <MethodDescription>Create cards entirely by hand</MethodDescription>
              </MethodButton>
              
              <MethodButton
                active={creationMethod === 'assisted'}
                onClick={() => setCreationMethod('assisted')}
              >
                <MethodIcon>🤝</MethodIcon>
                <MethodTitle>Assisted</MethodTitle>
                <MethodDescription>Enter a word, AI generates the rest</MethodDescription>
              </MethodButton>
              
              <MethodButton
                active={creationMethod === 'automatic'}
                onClick={() => setCreationMethod('automatic')}
              >
                <MethodIcon>🤖</MethodIcon>
                <MethodTitle>Automatic</MethodTitle>
                <MethodDescription>AI generates complete sets from topics</MethodDescription>
              </MethodButton>
            </CreationMethodSelector>
            
            {renderCreationMethod()}
          </>
        ) : activeTab === 'list' ? (
          <FlashcardList cards={cards} categories={categories} />
        ) : (
          <MyGroups />
        )}
      </ErrorBoundary>
    </Container>
  );
};

const Container = styled.div`
  padding: 16px;
`;

const TabContainer = styled.div`
  display: flex;
  margin-bottom: 20px;
  border-bottom: 1px solid #ddd;
`;

const Tab = styled.button`
  padding: 10px 20px;
  background: ${props => props.active ? props.theme.primary : 'transparent'};
  color: ${props => props.active ? props.theme.buttonText : props.theme.text};
  border: none;
  cursor: pointer;
  flex: 1;
  font-size: 16px;
  transition: background 0.3s;
  
  &:hover {
    background: ${props => props.active ? props.theme.primary : props.theme.backgroundLight};
  }
`;

const CreationMethodSelector = styled.div`
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 12px;
  margin-bottom: 24px;
`;

const MethodButton = styled.button`
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 16px 8px;
  border-radius: 8px;
  background: ${props => props.active ? props.theme.primaryLight : props.theme.backgroundLight};
  border: 2px solid ${props => props.active ? props.theme.primary : 'transparent'};
  cursor: pointer;
  transition: all 0.2s;
  
  &:hover {
    transform: translateY(-2px);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
`;

const MethodIcon = styled.div`
  font-size: 28px;
  margin-bottom: 12px;
`;

const MethodTitle = styled.h3`
  margin: 0 0 8px 0;
  font-size: 16px;
  font-weight: 600;
  color: ${props => props.theme.text};
`;

const MethodDescription = styled.p`
  margin: 0;
  font-size: 12px;
  color: ${props => props.theme.textLight};
  text-align: center;
`;

const AutomaticCreator = styled.div`
  background: ${props => props.theme.backgroundLight};
  border-radius: 8px;
  padding: 20px;
  margin-bottom: 20px;
`;

const GenerationForm = styled.div`
  display: flex;
  flex-direction: column;
  gap: 16px;
  
  label {
    display: flex;
    flex-direction: column;
    gap: 8px;
    
    span {
      font-weight: 500;
    }
    
    input {
      padding: 10px;
      border: 1px solid #ddd;
      border-radius: 4px;
      font-size: 16px;
    }
  }
`;

const GenerateButton = styled.button`
  padding: 12px;
  background: ${props => props.disabled ? props.theme.textLight : props.theme.accent};
  color: ${props => props.theme.buttonText};
  border: none;
  border-radius: 4px;
  font-size: 16px;
  font-weight: 600;
  cursor: ${props => props.disabled ? 'not-allowed' : 'pointer'};
  transition: background 0.3s;
  margin-top: 8px;
  
  &:hover {
    background: ${props => props.disabled ? props.theme.textLight : props.theme.accentDark};
  }
`;

const ErrorContainer = styled.div`
  color: ${props => props.theme.error};
  padding: 12px;
  background-color: ${props => `${props.theme.error}15`};
  border-radius: 4px;
  margin-bottom: 16px;
  border-left: 4px solid ${props => props.theme.error};
`;

const ErrorMessage = styled.div`
  font-weight: 600;
  margin-bottom: 4px;
`;

const ErrorDetails = styled.div`
  font-size: 14px;
  margin-bottom: 8px;
  font-family: monospace;
  white-space: pre-wrap;
  word-break: break-word;
`;

const ErrorTip = styled.div`
  font-size: 13px;
  font-style: italic;
  margin-top: 6px;
  color: ${props => props.theme.textLight};
`;

const NoteText = styled.p`
  font-size: 14px;
  color: ${props => props.theme.textLight};
  font-style: italic;
`;

const CardTypesLabel = styled.div`
  font-weight: 500;
  margin-top: 16px;
  margin-bottom: 8px;
`;

const CardTypesCheckboxes = styled.div`
  display: flex;
  gap: 16px;
  margin-bottom: 16px;
  flex-wrap: wrap;
`;

const CheckboxLabel = styled.label`
  display: flex;
  align-items: center;
  gap: 6px;
  cursor: pointer;
  color: ${props => props.theme.text};
  
  input {
    width: auto;
    cursor: pointer;
  }
`;

const ApiResponseDebuggerContainer = styled.div`
  margin-top: 20px;
  border-top: 1px solid ${props => props.theme.border};
  padding-top: 20px;
`;

// Add these new styled components
const CategorySelector = styled.div`
  display: flex;
  gap: 10px;
  
  select {
    flex: 1;
  }
`;

const CategoryInput = styled.div`
  display: flex;
  flex-direction: column;
  gap: 10px;
`;

const ButtonGroup = styled.div`
  display: flex;
  gap: 10px;
`;

const AddCategoryButton = styled.button`
  padding: 10px;
  background: ${props => props.theme.accent};
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background 0.3s;
  
  &:hover {
    background: ${props => props.theme.accentDark};
  }
`;

const AddButton = styled.button`
  padding: 8px 16px;
  background: ${props => props.theme.primary};
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  flex: 1;
`;

const CancelButton = styled.button`
  padding: 8px 16px;
  background: transparent;
  border: 1px solid ${props => props.theme.border};
  border-radius: 4px;
  cursor: pointer;
  flex: 1;
`;

const BatchProgressContainer = styled.div`
  margin: 16px 0;
  padding: 16px;
  background: ${props => props.theme.backgroundDark};
  border-radius: 8px;
`;

const BatchProgressHeader = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 12px;
  font-weight: 600;
`;

const BatchCounter = styled.span`
  font-weight: normal;
  color: ${props => props.theme.textLight};
  font-size: 0.9em;
`;

const ProgressTrack = styled.div`
  height: 8px;
  background: ${props => props.theme.background};
  border-radius: 4px;
  overflow: hidden;
  margin-bottom: 8px;
`;

const ProgressFill = styled.div`
  height: 100%;
  width: ${props => props.width || 0}%;
  background: ${props => props.theme.primary};
  transition: width 0.3s ease;
`;

const BatchDetails = styled.div`
  font-size: 14px;
  color: ${props => props.theme.textLight};
`;

export default FlashcardPage;
