import { store } from '../redux/store';
import * as wanakana from 'wanakana';
import { isDictionaryInstalled, downloadDictionary as downloadDict } from './dictionaryLoader';
import { setDictionaryState } from '../redux/slices/settingsSlice'; // Import for persistent dictionary state
import kuromoji from 'kuromoji';

// Kuromoji tokenizer instance (will be initialized)
let tokenizer = null;
let tokenizerPromise = null;
let tokenizerInitPromise = null;

// Add a state variable to track dictionary loading status
// We'll initialize it from redux state if available
let dictionaryStatus = {
  installed: false,
  loading: false,
  error: null
};

// Initialize dictionary status from saved state
const initDictionaryStatus = () => {
  try {
    const state = store.getState();
    if (state.settings && state.settings.dictionary) {
      dictionaryStatus = {
        ...dictionaryStatus,
        ...state.settings.dictionary
      };
      console.log('Dictionary status loaded from state:', dictionaryStatus);
    }
  } catch (e) {
    console.error('Failed to load dictionary status from state:', e);
  }
};

// Call this on service initialization
initDictionaryStatus();

/**
 * Get the current dictionary status
 */
export const getDictionaryStatus = () => {
  return { ...dictionaryStatus };
};

/**
 * Set the dictionary status and persist to Redux
 */
export const setDictionaryStatus = (status) => {
  dictionaryStatus = { ...dictionaryStatus, ...status };
  
  // Persist to Redux so it's saved between sessions
  try {
    store.dispatch(setDictionaryState(dictionaryStatus));
  } catch (e) {
    console.error('Failed to persist dictionary status:', e);
  }
};

/**
 * Initialize the kuromoji tokenizer
 * @returns {Promise} Promise that resolves when tokenizer is ready
 */
const initTokenizer = () => {
  if (tokenizer) {
    console.log('Tokenizer already initialized, reusing instance');
    return Promise.resolve(tokenizer);
  }
  
  if (tokenizerPromise) {
    console.log('Tokenizer initialization in progress, awaiting completion');
    return tokenizerPromise;
  }
  
  console.log('Initializing kuromoji tokenizer...');
  setDictionaryStatus({ loading: true, error: null });
  
  tokenizerPromise = new Promise((resolve, reject) => {
    // First check if dictionary files exist
    isDictionaryInstalled().then(installed => {
      if (!installed) {
        const error = new Error('Dictionary files not found. Please download the dictionary first.');
        setDictionaryStatus({ loading: false, installed: false, error: error.message });
        reject(error);
        return;
      }

      // Dictionary files exist, proceed with initialization
      console.log('Dictionary files found, loading kuromoji...');
      
      // Dynamic import to avoid loading kuromoji unnecessarily
      import('kuromoji').then(kuromoji => {
        // Use relative path to ensure files are found
        const dicPath = '/dict';
        console.log(`Building tokenizer with dictionary path: ${dicPath}`);
        
        kuromoji.builder({ dicPath }).build((err, _tokenizer) => {
          if (err) {
            console.error('Error initializing tokenizer:', err);
            setDictionaryStatus({ loading: false, error: err.message });
            tokenizerPromise = null; // Reset promise to allow retry
            reject(err);
            return;
          }
          
          tokenizer = _tokenizer;
          console.log('✅ Tokenizer initialized successfully!');
          setDictionaryStatus({ installed: true, loading: false, error: null });
          resolve(tokenizer);
        });
      }).catch(error => {
        console.error('Error loading kuromoji module:', error);
        setDictionaryStatus({ loading: false, error: error.message });
        tokenizerPromise = null; // Reset promise to allow retry
        reject(error);
      });
    }).catch(error => {
      console.error('Error checking dictionary installation:', error);
      setDictionaryStatus({ loading: false, error: error.message });
      tokenizerPromise = null; // Reset promise to allow retry
      reject(error);
    });
  });
  
  return tokenizerPromise;
};

/**
 * Check if text contains kanji
 */
const hasKanji = (text) => {
  return /[\u4E00-\u9FAF]/.test(text);
};

/**
 * Check if text is likely Japanese
 */
const isJapaneseText = (text) => {
  // Check for Japanese characters (hiragana, katakana, kanji)
  return /[\u3040-\u309F\u30A0-\u30FF\u4E00-\u9FAF]/.test(text);
};

/**
 * Generate detailed furigana info for Japanese text
 */
export const generateFurigana = async (text) => {
  if (!text || !isJapaneseText(text)) return { segments: [{ text }], furigana: {} };
  
  try {
    // Initialize tokenizer before trying to use it
    await initTokenizer();
    
    if (!tokenizer) {
      console.error('Tokenizer not initialized, falling back to dictionary');
      // Use fallback if tokenizer isn't available
      const fallbackMap = getFallbackFurigana(text);
      return { 
        segments: [{ text }], 
        furigana: fallbackMap
      };
    }
    
    console.log(`Generating furigana for: "${text.substring(0, 20)}${text.length > 20 ? '...' : ''}"`);
    
    // Tokenize the text using kuromoji
    const tokens = tokenizer.tokenize(text);
    console.log(`Tokenized into ${tokens.length} tokens`);
    
    // Process tokens to generate furigana
    const segments = [];
    const furiganaMap = {};
    
    tokens.forEach(token => {
      const { surface_form, reading, pos, pos_detail_1 } = token;
      
      // Skip punctuation and symbols
      if (pos === '記号' || pos_detail_1 === '空白') {
        segments.push({ text: surface_form });
        return;
      }
      
      // If token has kanji and a reading, add furigana
      if (hasKanji(surface_form) && reading) {
        const hiraganaReading = katakanaToHiragana(reading);
        
        segments.push({
          text: surface_form,
          reading: hiraganaReading
        });
        
        furiganaMap[surface_form] = hiraganaReading;
      } else {
        // No kanji or no reading, just use the surface form
        segments.push({ text: surface_form });
      }
    });
    
    return { segments, furigana: furiganaMap };
  } catch (error) {
    console.error('Error generating furigana with tokenizer:', error);
    // Fallback to dictionary-based furigana
    console.log('Falling back to dictionary-based furigana');
    const fallbackMap = getFallbackFurigana(text);
    return { 
      segments: [{ text }], 
      furigana: fallbackMap
    };
  }
};

/**
 * Convert katakana to hiragana for readings
 */
const katakanaToHiragana = (text) => {
  try {
    // If wanakana is available, use it for conversion
    if (wanakana && typeof wanakana.toHiragana === 'function') {
      return wanakana.toHiragana(text);
    }
    
    // Fallback conversion if wanakana fails
    return text.replace(/[\u30A1-\u30F6]/g, match => {
      const code = match.charCodeAt(0) - 0x60;
      return String.fromCharCode(code);
    });
  } catch (e) {
    console.error('Error converting katakana to hiragana:', e);
    return text;
  }
};

/**
 * Process a single flashcard for Japanese content
 */
export const processJapaneseFlashcard = async (card) => {
  const isJapanese = store.getState().settings.learningLanguage === 'ja';
  if (!isJapanese) return card;
  
  try {
    // Add safety check for empty card
    if (!card) return card;
    
    // Add a text length limit to prevent hanging on very large texts
    const maxLength = 5000;
    const limitText = (text) => {
      if (!text) return '';
      return text.length > maxLength ? text.substring(0, maxLength) + '...' : text;
    };
    
    // Apply length limiting
    const safeCard = {
      ...card,
      front: limitText(card.front),
      back: limitText(card.back),
      sentenceExample: limitText(card.sentenceExample),
      sentence: card.type === 'fill' ? limitText(card.sentence) : card.sentence
    };
    
    // Process each text field with a timeout to prevent hanging
    const frontAnalysisPromise = generateFuriganaWithTimeout(safeCard.front);
    const backAnalysisPromise = generateFuriganaWithTimeout(safeCard.back);
    
    // Process in parallel for better performance
    const [frontAnalysis, backAnalysis] = await Promise.all([frontAnalysisPromise, backAnalysisPromise]);
    
    // Only process example and sentence if they exist
    let sentenceExampleAnalysis = null;
    if (safeCard.sentenceExample) {
      sentenceExampleAnalysis = await generateFuriganaWithTimeout(safeCard.sentenceExample);
    }
    
    let sentenceAnalysis = null;
    if (safeCard.type === 'fill' && safeCard.sentence) {
      sentenceAnalysis = await generateFuriganaWithTimeout(safeCard.sentence);
    }
    
    // Ensure furigana objects are never undefined
    return { 
      ...safeCard,
      frontFurigana: frontAnalysis?.furigana || {},
      backFurigana: backAnalysis?.furigana || {},
      sentenceExampleFurigana: sentenceExampleAnalysis?.furigana || {},
      sentenceFurigana: sentenceAnalysis?.furigana || {}
    };
  } catch (error) {
    console.error('Error processing Japanese flashcard:', error);
    // Provide empty furigana objects to avoid UI errors
    return {
      ...card,
      frontFurigana: {},
      backFurigana: {},
      sentenceExampleFurigana: {},
      sentenceFurigana: {}
    };
  }
};

/**
 * Generate furigana with a timeout to prevent UI hanging
 */
const generateFuriganaWithTimeout = async (text, timeout = 2000) => {
  if (!text) return { segments: [], furigana: {} };
  
  // Create a promise that resolves after the timeout
  const timeoutPromise = new Promise(resolve => {
    setTimeout(() => {
      resolve({ segments: [{ text }], furigana: {} });
    }, timeout);
  });
  
  // Race the actual processing against the timeout
  return Promise.race([
    generateFurigana(text),
    timeoutPromise
  ]);
};

/**
 * Process an array of flashcards for Japanese content
 */
export const processJapaneseFlashcards = async (cards) => {
  const isJapanese = store.getState().settings.learningLanguage === 'ja';
  if (!isJapanese) return cards;
  
  try {
    // Process cards in parallel
    const processedCards = await Promise.all(
      cards.map(card => processJapaneseFlashcard(card))
    );
    
    return processedCards;
  } catch (error) {
    console.error('Error processing Japanese flashcards:', error);
    return cards;
  }
};

