/**
 * Get fallback furigana when kuromoji isn't initialized yet
 */
export const getFallbackFurigana = (text) => {
  if (!text) return {};
  if (store.getState().settings.learningLanguage !== 'ja') return {};
  
  const furiganaMap = {};
  
  // Check all entries in our dictionary
  Object.keys(fallbackDictionary).forEach(kanji => {
    if (text.includes(kanji) && hasKanji(kanji)) {
      furiganaMap[kanji] = fallbackDictionary[kanji];
    }
  });
  
  return furiganaMap;
};

/**
 * Download dictionary files
 */
export const downloadDictionary = async (progressCallback) => {
  try {
    setDictionaryStatus({ loading: true });
    
    // Check if dictionary is already installed
    const installed = await isDictionaryInstalled();
    if (installed) {
      setDictionaryStatus({ installed: true, loading: false });
      if (progressCallback) progressCallback({ message: 'Dictionary already installed', progress: 100 });

      // Even if installed, make sure tokenizer is initialized
      try {
        await initTokenizer();
        console.log('Tokenizer initialized after dictionary found to be installed');
      } catch (initError) {
        console.error('Failed to initialize tokenizer even though dictionary is installed:', initError);
      }
      
      return true;
    }
    
    // Otherwise, perform the download
    if (progressCallback) progressCallback({ message: 'Downloading dictionary...', progress: 10 });
    
    // Call the downloadDictionary function from dictionaryLoader
    await downloadDict(progressCallback);
    
    // Initialize the tokenizer with the newly downloaded dictionary
    if (progressCallback) progressCallback({ message: 'Initializing dictionary...', progress: 90 });
    
    try {
      await initTokenizer();
      console.log('Tokenizer successfully initialized after dictionary download!');
    } catch (initError) {
      console.error('Failed to initialize tokenizer after download:', initError);
      throw initError;
    }
    
    if (progressCallback) progressCallback({ message: 'Dictionary installed successfully', progress: 100 });
    
    return true;
  } catch (error) {
    console.error('Error downloading dictionary:', error);
    setDictionaryStatus({ loading: false, error: error.message });
    throw error;
  }
};



/**
 * Tokenize Japanese text and extract furigana readings
 */
export const tokenizeJapanese = async (text) => {
  if (!text) return [];
  
  try {
    // Initialize tokenizer if needed
    if (!tokenizer) {
      await initTokenizer();
    }
    
    // Tokenize the text
    const tokens = tokenizer.tokenize(text);
    
    // Process tokens to extract readings
    return tokens.map(token => {
      // Extract the reading only for kanji words (where reading differs from surface form)
      const hasKanji = /[\u4e00-\u9faf]/.test(token.surface_form);
      const needsReading = hasKanji && token.reading !== token.surface_form;
      
      return {
        text: token.surface_form,
        reading: needsReading ? kataToHira(token.reading) : null
      };
    });
  } catch (error) {
    console.error('Error tokenizing Japanese text:', error);
    // Fall back to returning the text without readings
    return [{ text, reading: null }];
  }
};

/**
 * Convert katakana to hiragana (for furigana display)
 */
const kataToHira = (kata) => {
  if (!kata) return kata;
  return kata.replace(/[\u30A1-\u30FA]/g, ch => 
    String.fromCharCode(ch.charCodeAt(0) - 0x60)
  );
};




/**
 * Extract a mapping of words to their furigana readings from text
 */
const extractFuriganaMap = async (text) => {
  if (!text) return {};
  
  try {
    // Initialize tokenizer if needed
    if (!tokenizer) {
      await initTokenizer();
    }
    
    // Tokenize the text
    const tokens = tokenizer.tokenize(text);
    
    // Create a mapping of words to their readings
    const furiganaMap = {};
    for (const token of tokens) {
      const hasKanji = /[\u4e00-\u9faf]/.test(token.surface_form);
      if (hasKanji && token.reading !== token.surface_form) {
        furiganaMap[token.surface_form] = kataToHira(token.reading);
      }
    }
    
    return furiganaMap;
  } catch (error) {
    console.error('Error extracting furigana map:', error);
    return {};
  }
};
