import { createSlice } from '@reduxjs/toolkit';
import { loadFromLocalStorage, saveToLocalStorage } from '../../services/localStorageService';

const initialState = loadFromLocalStorage('flashcards') || {
  cards: [],
  categories: [],
  isLoading: false,
  error: null,
};

/**
 * Generate a truly unique ID using timestamp, random values, and browser-specific information
 * This mimics UUID v4 generation without requiring additional libraries
 */
const generateUniqueId = () => {
  // Get high-precision timestamp
  const timestamp = Date.now();
  
  // Create four random components
  const a = Math.floor(Math.random() * 0xffff);
  const b = Math.floor(Math.random() * 0xffff);
  const c = Math.floor(Math.random() * 0xffff);
  const d = Math.floor(Math.random() * 0xffff);
  
  // Format with dashes to look like a UUID
  return `${timestamp}-${a.toString(16)}-${b.toString(16)}-${c.toString(16)}-${d.toString(16)}`;
};

const flashcardsSlice = createSlice({
  name: 'flashcards',
  initialState,
  reducers: {
    addFlashcard: (state, action) => {
      const newCard = {
        ...action.payload,
        id: generateUniqueId(), // This now uses our more robust ID generator
        createdAt: new Date().toISOString(),
        lastReviewed: null,
        nextReview: new Date().toISOString(),
        reviewCount: 0,
        easeFactor: 2.5,
        interval: 1,
      };
      state.cards.push(newCard);
      saveToLocalStorage('flashcards', state);
    },
    // Modified updateFlashcard with additional safety
    updateFlashcard: (state, action) => {
      try {
        const index = state.cards.findIndex(card => card.id === action.payload.id);
        if (index !== -1) {
          // Create a safe copy of the payload
          const safePayload = { ...action.payload };
          
          // Ensure furigana fields exist
          if (!safePayload.frontFurigana) safePayload.frontFurigana = {};
          if (!safePayload.backFurigana) safePayload.backFurigana = {};
          if (!safePayload.sentenceExampleFurigana) safePayload.sentenceExampleFurigana = {};
          if (!safePayload.sentenceFurigana) safePayload.sentenceFurigana = {};
          
          // Create a completely new object to avoid reference issues
          state.cards[index] = { 
            ...state.cards[index], 
            ...safePayload
          };
          
          saveToLocalStorage('flashcards', state);
        }
      } catch (error) {
        console.error("Error in updateFlashcard reducer:", error);
        // Don't save to localStorage if there was an error
      }
    },
    deleteFlashcard: (state, action) => {
      state.cards = state.cards.filter(card => card.id !== action.payload);
      saveToLocalStorage('flashcards', state);
    },
    reviewFlashcard: (state, action) => {
      const { id, quality } = action.payload;
      const cardIndex = state.cards.findIndex(card => card.id === id);
      
      if (cardIndex !== -1) {
        const card = state.cards[cardIndex];
        const { interval, easeFactor } = calculateNextReview(card, quality);
        
        state.cards[cardIndex] = {
          ...card,
          lastReviewed: new Date().toISOString(),
          nextReview: new Date(Date.now() + interval * 24 * 60 * 60 * 1000).toISOString(),
          reviewCount: card.reviewCount + 1,
          easeFactor,
          interval,
        };
        
        saveToLocalStorage('flashcards', state);
      }
    },
    addCategory: (state, action) => {
      if (!state.categories.includes(action.payload)) {
        state.categories.push(action.payload);
        saveToLocalStorage('flashcards', state);
      }
    },
    deleteCategory: (state, action) => {
      const categoryToDelete = action.payload;
      
      // Remove the category from the list
      state.categories = state.categories.filter(cat => cat !== categoryToDelete);
      
      // Delete all cards in this category
      state.cards = state.cards.filter(card => card.category !== categoryToDelete);
      
      // Set cards without a category to "Uncategorized"
      state.cards = state.cards.map(card => {
        if (!state.categories.includes(card.category)) {
          return { ...card, category: 'Uncategorized' };
        }
        return card;
      });
      
      // Add "Uncategorized" if it's not already there
      if (state.cards.some(card => card.category === 'Uncategorized') && 
          !state.categories.includes('Uncategorized')) {
        state.categories.push('Uncategorized');
      }
    }
  },
});

// SM-2 algorithm for spaced repetition
function calculateNextReview(card, quality) {
  let { easeFactor, interval } = card;
  
  // Update ease factor
  easeFactor = Math.max(1.3, easeFactor + (0.1 - (5 - quality) * (0.08 + (5 - quality) * 0.02)));
  
  // Calculate next interval
  if (quality < 3) {
    interval = 1; // Reset interval if quality < 3 (failed)
  } else {
    interval = card.reviewCount === 0 ? 1 : card.reviewCount === 1 ? 6 : Math.round(interval * easeFactor);
  }
  
  return { interval, easeFactor };
}

export const { addFlashcard, updateFlashcard, deleteFlashcard, reviewFlashcard, addCategory, deleteCategory } = flashcardsSlice.actions;
export default flashcardsSlice.reducer;
