import React, { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import styled from 'styled-components';
import { addFlashcard, addCategory } from '../../redux/slices/flashcardsSlice';

const FlashcardCreator = () => {
  const dispatch = useDispatch();
  const { categories } = useSelector(state => state.flashcards);
  const { learningLanguage } = useSelector(state => state.settings);
  
  const [cardType, setCardType] = useState('classic');
  const [front, setFront] = useState('');
  const [back, setBack] = useState('');
  const [category, setCategory] = useState('');
  const [newCategory, setNewCategory] = useState('');
  const [isAddingCategory, setIsAddingCategory] = useState(false);
  
  // For multiple choice
  const [options, setOptions] = useState(['', '', '']);
  const [correctIndex, setCorrectIndex] = useState(0);
  
  // For fill-in-the-blank
  const [sentence, setSentence] = useState('');
  const [answer, setAnswer] = useState('');
  
  // Add sentence example for all card types
  const [sentenceExample, setSentenceExample] = useState('');
  // Add new state for sentence example translation
  const [sentenceExampleTranslation, setSentenceExampleTranslation] = useState('');
  
  const resetForm = () => {
    setFront('');
    setBack('');
    setOptions(['', '', '']);
    setCorrectIndex(0);
    setSentence('');
    setAnswer('');
    setSentenceExample(''); // Reset the example sentence
    setSentenceExampleTranslation(''); // Reset the example sentence translation
  };
  
  const handleAddFlashcard = () => {
    if (!validateForm()) return;
    
    const newCard = {
      type: cardType,
      front,
      back,
      category: category || 'Uncategorized',
      sentenceExample: sentenceExample || '',
      sentenceExampleTranslation: sentenceExampleTranslation || '', // Add the translation field
    };
    
    if (cardType === 'multiple') {
      newCard.multipleChoice = {
        options,
        correctIndex
      };
    } else if (cardType === 'fill') {
      newCard.sentence = sentence;
      newCard.answer = answer;
    }
    
    dispatch(addFlashcard(newCard));
    
    if (category && !categories.includes(category)) {
      dispatch(addCategory(category));
    }
    
    resetForm();
  };
  
  const validateForm = () => {
    if (!front || !back) return false;
    
    if (cardType === 'multiple') {
      if (options.some(opt => !opt.trim())) return false;
    } else if (cardType === 'fill') {
      if (!sentence || !answer) return false;
    }
    
    return true;
  };
  
  const handleOptionChange = (index, value) => {
    const newOptions = [...options];
    newOptions[index] = value;
    setOptions(newOptions);
  };
  
  const handleAddCategory = () => {
    if (newCategory.trim()) {
      dispatch(addCategory(newCategory.trim()));
      setCategory(newCategory.trim());
      setNewCategory('');
      setIsAddingCategory(false);
    }
  };

  return (
    <Container>
      <CardTypeSelector>
        <CardTypeButton 
          active={cardType === 'classic'} 
          onClick={() => setCardType('classic')}
        >
          Anki-style
        </CardTypeButton>
        <CardTypeButton 
          active={cardType === 'multiple'} 
          onClick={() => setCardType('multiple')}
        >
          Multiple Choice
        </CardTypeButton>
        <CardTypeButton 
          active={cardType === 'fill'} 
          onClick={() => setCardType('fill')}
        >
          Fill in the Blank
        </CardTypeButton>
      </CardTypeSelector>
      
      <FormGroup>
        <label>
          Front (Question/Prompt):
          <input
            type="text"
            value={front}
            onChange={(e) => setFront(e.target.value)}
            placeholder={`e.g., Word in ${learningLanguage}`}
          />
        </label>
      </FormGroup>
      
      <FormGroup>
        <label>
          Back (Answer/Definition):
          <input
            type="text"
            value={back}
            onChange={(e) => setBack(e.target.value)}
            placeholder="e.g., Translation or explanation"
          />
        </label>
      </FormGroup>
      
      {/* Add sentence example field for all card types */}
      <FormGroup>
        <label>
          Example Sentence (Optional):
          <input
            type="text"
            value={sentenceExample}
            onChange={(e) => setSentenceExample(e.target.value)}
            placeholder="e.g., A sentence showing usage of this word/phrase"
          />
        </label>
      </FormGroup>
      
      {/* Add new field for sentence example translation */}
      <FormGroup>
        <label>
          Example Sentence Translation (Optional):
          <input
            type="text"
            value={sentenceExampleTranslation}
            onChange={(e) => setSentenceExampleTranslation(e.target.value)}
            placeholder="e.g., English translation of the example sentence"
          />
        </label>
      </FormGroup>
      
      {cardType === 'multiple' && (
        <MultipleChoiceSection>
          <h3>Multiple Choice Options:</h3>
          {options.map((option, index) => (
            <OptionRow key={index}>
              <RadioInput
                type="radio"
                checked={correctIndex === index}
                onChange={() => setCorrectIndex(index)}
              />
              <input
                type="text"
                value={option}
                onChange={(e) => handleOptionChange(index, e.target.value)}
                placeholder={index === correctIndex ? "Correct answer" : `Incorrect option ${index + 1}`}
              />
            </OptionRow>
          ))}
          <Note>* Select the radio button next to the correct option</Note>
        </MultipleChoiceSection>
      )}
      
      {cardType === 'fill' && (
        <FillBlankSection>
          <FormGroup>
            <label>
              Sentence with blank (use ___ for blank):
              <input
                type="text"
                value={sentence}
                onChange={(e) => setSentence(e.target.value)}
                placeholder="e.g., I ___ to the store yesterday."
              />
            </label>
          </FormGroup>
          <FormGroup>
            <label>
              Correct answer for blank:
              <input
                type="text"
                value={answer}
                onChange={(e) => setAnswer(e.target.value)}
                placeholder="e.g., went"
              />
            </label>
          </FormGroup>
        </FillBlankSection>
      )}
      
      <FormGroup>
        <label>
          Category:
          {!isAddingCategory ? (
            <CategorySelector>
              <select 
                value={category} 
                onChange={(e) => setCategory(e.target.value)}
              >
                <option value="">Select a category</option>
                {categories.map((cat, index) => (
                  <option key={index} value={cat}>{cat}</option>
                ))}
              </select>
              <AddCategoryButton onClick={() => setIsAddingCategory(true)}>
                + New
              </AddCategoryButton>
            </CategorySelector>
          ) : (
            <CategoryInput>
              <input
                type="text"
                value={newCategory}
                onChange={(e) => setNewCategory(e.target.value)}
                placeholder="New category name"
              />
              <ButtonGroup>
                <AddButton onClick={handleAddCategory}>Add</AddButton>
                <CancelButton onClick={() => setIsAddingCategory(false)}>Cancel</CancelButton>
              </ButtonGroup>
            </CategoryInput>
          )}
        </label>
      </FormGroup>
      
      <CreateButtonContainer>
        <CreateButton 
          onClick={handleAddFlashcard}
          disabled={!validateForm()}
        >
          Create Flashcard
        </CreateButton>
      </CreateButtonContainer>
    </Container>
  );
};

const Container = styled.div`
  background: ${props => props.theme.backgroundLight};
  border-radius: 8px;
  padding: 20px;
  margin-bottom: 20px;
`;

const CardTypeSelector = styled.div`
  display: flex;
  margin-bottom: 20px;
  border-radius: 6px;
  overflow: hidden;
  border: 1px solid ${props => props.theme.border};
`;

const CardTypeButton = styled.button`
  background: ${props => props.active ? props.theme.primary : 'transparent'};
  color: ${props => props.active ? 'white' : props.theme.text};
  border: none;
  padding: 10px 0;
  flex: 1;
  cursor: pointer;
  transition: background 0.3s;
  font-weight: ${props => props.active ? '600' : '400'};
  
  &:hover {
    background: ${props => props.active ? props.theme.primary : props.theme.backgroundDark};
  }
`;

const FormGroup = styled.div`
  margin-bottom: 16px;
  
  label {
    display: block;
    margin-bottom: 6px;
    font-weight: 500;
    color: ${props => props.theme.text};
  }
  
  input, select, textarea {
    width: 100%;
    padding: 10px;
    border: 1px solid ${props => props.theme.border};
    border-radius: 4px;
    font-size: 16px;
    background: ${props => props.theme.backgroundInput};
    color: ${props => props.theme.text};
  }
`;

const MultipleChoiceSection = styled.div`
  margin-bottom: 20px;
  
  h3 {
    font-size: 16px;
    margin-bottom: 12px;
    font-weight: 500;
  }
`;

const OptionRow = styled.div`
  display: flex;
  align-items: center;
  margin-bottom: 10px;
  
  input[type="text"] {
    flex: 1;
  }
`;

const RadioInput = styled.input`
  margin-right: 10px;
  width: 18px;
  height: 18px;
  cursor: pointer;
  accent-color: ${props => props.theme.primary}; // Set color for radio buttons
`;

const FillBlankSection = styled.div`
  margin-bottom: 20px;
`;

const CategorySelector = styled.div`
  display: flex;
  gap: 10px;
  
  select {
    flex: 1;
  }
`;

const CategoryInput = styled.div`
  display: flex;
  flex-direction: column;
  gap: 10px;
`;

const ButtonGroup = styled.div`
  display: flex;
  gap: 10px;
`;

const AddCategoryButton = styled.button`
  padding: 10px;
  background: ${props => props.theme.accent};
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background 0.3s;
  
  &:hover {
    background: ${props => props.theme.accentDark};
  }
`;

const AddButton = styled.button`
  padding: 8px 16px;
  background: ${props => props.theme.primary};
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  flex: 1;
`;

const CancelButton = styled.button`
  padding: 8px 16px;
  background: transparent;
  border: 1px solid ${props => props.theme.border};
  border-radius: 4px;
  cursor: pointer;
  flex: 1;
`;

const CreateButtonContainer = styled.div`
  margin-top: 20px;
  text-align: right;
`;

const CreateButton = styled.button`
  padding: 12px 24px;
  background: ${props => props.disabled ? '#ccc' : props.theme.primary};
  color: white;
  border: none;
  border-radius: 4px;
  font-size: 16px;
  font-weight: 600;
  cursor: ${props => props.disabled ? 'not-allowed' : 'pointer'};
  transition: all 0.3s;
  
  &:hover {
    background: ${props => props.disabled ? '#ccc' : props.theme.primaryDark};
  }
`;

const Note = styled.p`
  font-size: 14px;
  color: ${props => props.theme.textLight};
  font-style: italic;
  margin-top: 8px;
`;

export default FlashcardCreator;
