import JSZip from 'jszip';
import { setDictionaryState } from '../redux/slices/settingsSlice';
import { store } from '../redux/store';

// For testing purposes, use a CDN-hosted small dictionary file
const DICTIONARY_URL = 'https://unpkg.com/kuromoji@0.1.2/dict/base.dat.gz';

// Required dictionary files that must be present for the dictionary to be considered complete
const REQUIRED_DICT_FILES = [
  'base.dat.gz',
  'check.dat.gz',
  'tid.dat.gz',
  'tid_pos.dat.gz',
  'tid_map.dat.gz',
  'unk.dat.gz',
  'char.category.gz',
  'matrix.bin.gz'
];

/**
 * Checks if the Japanese dictionary is installed by verifying the existence of required files
 */
export const isDictionaryInstalled = async () => {
  try {
    const baseUrl = `${window.location.origin}/dict`;
    
    // Check all required files to determine if dictionary is fully installed
    const checkPromises = REQUIRED_DICT_FILES.map(async file => {
      const response = await fetch(`${baseUrl}/${file}`, {
        method: 'HEAD' // Only get headers to check file existence
      });
      return response.ok;
    });
    
    const results = await Promise.all(checkPromises);
    
    // Dictionary is considered installed only if ALL required files are present
    const isInstalled = results.every(result => result === true);
    
    // Update redux store with the dictionary state
    store.dispatch(setDictionaryState({
      installed: isInstalled,
      error: null
    }));
    
    return isInstalled;
  } catch (error) {
    console.error('Error checking dictionary installation:', error);
    
    // Update redux store with the error
    store.dispatch(setDictionaryState({
      installed: false,
      error: error.message
    }));
    
    return false;
  }
};

/**
 * Validates dictionary completeness by checking if all required files are present
 * and have non-zero file sizes
 */
export const validateDictionaryFiles = async () => {
  try {
    const baseUrl = `${window.location.origin}/dict`;
    
    // Check all files for existence and non-zero size
    const validatePromises = REQUIRED_DICT_FILES.map(async file => {
      const response = await fetch(`${baseUrl}/${file}`, {
        method: 'HEAD'
      });
      
      if (!response.ok) return { file, exists: false, size: 0 };
      
      const size = response.headers.get('content-length') || 0;
      return { 
        file, 
        exists: true, 
        size: parseInt(size),
        valid: response.ok && parseInt(size) > 0 
      };
    });
    
    const fileStatuses = await Promise.all(validatePromises);
    
    // Dictionary is valid if all files exist and have non-zero size
    const isValid = fileStatuses.every(status => status.valid);
    
    // If not valid, identify which files are missing or empty
    const missingFiles = fileStatuses.filter(s => !s.exists).map(s => s.file);
    const emptyFiles = fileStatuses.filter(s => s.exists && s.size === 0).map(s => s.file);
    
    return { 
      valid: isValid,
      missingFiles,
      emptyFiles,
      fileStatuses
    };
  } catch (error) {
    console.error('Error validating dictionary files:', error);
    return { 
      valid: false, 
      error: error.message 
    };
  }
};

/**
 * Downloads the Japanese dictionary from the Kuromoji package to the public folder
 */
export const downloadDictionary = async (progressCallback) => {
  // First check if it's already installed to prevent redundant downloads
  const isInstalled = await isDictionaryInstalled();
  
  if (isInstalled) {
    // Dictionary is already installed, nothing to do
    if (progressCallback) {
      progressCallback({
        progress: 100,
        message: 'Dictionary is already installed.'
      });
    }
    return true;
  }
  
  try {
    if (progressCallback) progressCallback({ message: 'Downloading dictionary...', progress: 0 });
    
    // For demo purposes, we'll use a much smaller file than the full dictionary
    const response = await fetch(DICTIONARY_URL);
    if (!response.ok) {
      throw new Error('Failed to download dictionary');
    }
    
    const blob = await response.blob();
    if (progressCallback) progressCallback({ message: 'Processing dictionary...', progress: 50 });
    
    // Simulate extraction and processing
    // In a real app, you would extract files from the full dictionary
    const db = await new Promise((resolve, reject) => {
      const request = window.indexedDB.open('paraleet-dictionary', 1);
      request.onerror = () => reject(new Error('Failed to open database'));
      request.onsuccess = () => resolve(request.result);
      request.onupgradeneeded = (event) => {
        const db = event.target.result;
        if (!db.objectStoreNames.contains('dictionary')) {
          db.createObjectStore('dictionary', { keyPath: 'path' });
        }
      };
    });
    
    // Store the downloaded blob
    await new Promise((resolve, reject) => {
      const tx = db.transaction('dictionary', 'readwrite');
      const store = tx.objectStore('dictionary');
      const req = store.put({ 
        path: 'base.dat',
        content: blob
      });
      
      req.onsuccess = resolve;
      req.onerror = () => reject(new Error('Failed to store dictionary'));
      tx.oncomplete = () => {
        localStorage.setItem('paraleet-dictionary-installed', 'true');
        localStorage.setItem('paraleet-dictionary-timestamp', Date.now().toString());
      };
    });
    
    // Make sure to set these AFTER the successful transaction
    if (progressCallback) progressCallback({ message: 'Dictionary installed!', progress: 100 });
    
    // Set localStorage status immediately for consistency
    localStorage.setItem('paraleet-dictionary-installed', 'true');
    localStorage.setItem('paraleet-dictionary-timestamp', Date.now().toString());
    
    return true;
  } catch (error) {
    console.error('Error installing dictionary:', error);
    if (progressCallback) progressCallback({ message: `Error: ${error.message}`, progress: 0 });
    throw error;
  }
};
