import React, { useState, useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import styled from 'styled-components';
import { setDictionaryState } from '../../redux/slices/settingsSlice';
import { isDictionaryInstalled, downloadDictionary, validateDictionaryFiles } from '../../services/dictionaryLoader';

const DictionaryManagement = () => {
  const dispatch = useDispatch();
  const { dictionary } = useSelector(state => state.settings);
  const [downloadProgress, setDownloadProgress] = useState(0);
  const [downloadStatus, setDownloadStatus] = useState('');
  const [isChecking, setIsChecking] = useState(true);
  const [validationResult, setValidationResult] = useState(null);

  // Check if dictionary is installed on component load
  useEffect(() => {
    checkDictionary();
  }, []);

  const checkDictionary = async () => {
    setIsChecking(true);
    try {
      // Check if dictionary is installed
      const isInstalled = await isDictionaryInstalled();
      
      // If installed, also validate the files
      if (isInstalled) {
        const validation = await validateDictionaryFiles();
        setValidationResult(validation);
        
        // If validation failed, mark as not installed
        if (!validation.valid) {
          dispatch(setDictionaryState({ 
            installed: false,
            error: 'Dictionary files are incomplete or corrupted'
          }));
        }
      }
    } catch (error) {
      console.error('Error checking dictionary status:', error);
    } finally {
      setIsChecking(false);
    }
  };

  const handleDownload = async () => {
    // Prevent download if already installed
    if (dictionary.installed) {
      setDownloadStatus('Dictionary is already installed');
      return;
    }

    setDownloadProgress(0);
    setDownloadStatus('Starting download...');

    try {
      await downloadDictionary(status => {
        setDownloadProgress(status.progress);
        setDownloadStatus(status.message);
      });
      
      // Verify the installation after download
      await checkDictionary();
    } catch (error) {
      console.error('Error downloading dictionary:', error);
      setDownloadStatus(`Error: ${error.message}`);
      dispatch(setDictionaryState({
        installed: false,
        error: error.message
      }));
    }
  };

  return (
    <SettingRow>
      <SettingLabel>Japanese Dictionary</SettingLabel>
      <SettingControl>
        {isChecking ? (
          <StatusMessage>Checking dictionary status...</StatusMessage>
        ) : dictionary.installed ? (
          <>
            <DictionaryStatus installed>
              ✓ Dictionary installed
              {validationResult && !validationResult.valid && (
                <WarningText>Warning: Some files may be incomplete</WarningText>
              )}
            </DictionaryStatus>
            <ActionButton onClick={checkDictionary}>
              Verify Installation
            </ActionButton>
          </>
        ) : (
          <>
            <DictionaryStatus installed={false}>
              ✗ Dictionary not installed
            </DictionaryStatus>
            <ActionButton 
              onClick={handleDownload} 
              disabled={downloadProgress > 0 && downloadProgress < 100}
            >
              {downloadProgress > 0 ? `Downloading... ${downloadProgress}%` : 'Install Dictionary'}
            </ActionButton>
          </>
        )}

        {downloadProgress > 0 && (
          <ProgressIndicator>
            <ProgressBar>
              <ProgressFill width={downloadProgress} />
            </ProgressBar>
            <StatusMessage>{downloadStatus}</StatusMessage>
          </ProgressIndicator>
        )}
      </SettingControl>
    </SettingRow>
  );
};

// Styled components
const SettingRow = styled.div`
  display: flex;
  padding: 12px 16px;
  border-bottom: 1px solid ${props => props.theme.border};
  flex-direction: column;
`;

const SettingLabel = styled.div`
  font-weight: 500;
  margin-bottom: 8px;
`;

const SettingControl = styled.div`
  display: flex;
  flex-direction: column;
  gap: 10px;
`;

const DictionaryStatus = styled.div`
  color: ${props => props.installed ? props.theme.success : props.theme.error};
  font-weight: 500;
`;

const WarningText = styled.div`
  color: ${props => props.theme.accent};
  font-size: 0.85em;
  margin-top: 4px;
`;

const ActionButton = styled.button`
  padding: 8px 16px;
  background: ${props => props.theme.primary};
  color: white;
  border: none;
  border-radius: 4px;
  cursor: ${props => props.disabled ? 'not-allowed' : 'pointer'};
  opacity: ${props => props.disabled ? 0.7 : 1};
  margin-top: 10px;
`;

const ProgressIndicator = styled.div`
  width: 100%;
  margin-top: 10px;
`;

const ProgressBar = styled.div`
  height: 8px;
  background: ${props => props.theme.backgroundDark};
  border-radius: 4px;
  overflow: hidden;
`;

const ProgressFill = styled.div`
  height: 100%;
  width: ${props => props.width || 0}%;
  background: ${props => props.theme.primary};
  transition: width 0.3s ease;
`;

const StatusMessage = styled.div`
  font-size: 14px;
  color: ${props => props.theme.textLight};
  margin-top: 5px;
  font-style: italic;
`;

export default DictionaryManagement;
