import React, { useState, useEffect } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';
import styled from 'styled-components';
import { reviewFlashcard } from '../redux/slices/flashcardsSlice';
import { completeLevel, increaseStreak, masterCard } from '../redux/slices/studySlice';
import FuriganaText from '../components/Japanese/FuriganaText';
import { processJapaneseFlashcards } from '../services/japaneseService';

const StudySessionPage = () => {
  const { levelId } = useParams();
  const navigate = useNavigate();
  const dispatch = useDispatch();
  
  const { cards } = useSelector(state => state.flashcards);
  const { levels, masteredCards } = useSelector(state => state.study);
  const { cardAdvanceMode } = useSelector(state => state.settings);
  
  const [currentCardIndex, setCurrentCardIndex] = useState(0);
  const [showAnswer, setShowAnswer] = useState(false);
  const [selectedOption, setSelectedOption] = useState(null);
  const [userInput, setUserInput] = useState('');
  const [isCorrect, setIsCorrect] = useState(null);
  const [sessionCards, setSessionCards] = useState([]);
  const [sessionCompleted, setSessionCompleted] = useState(false);
  const [sessionStats, setSessionStats] = useState({
    total: 0,
    correct: 0,
    incorrect: 0,
    mastered: 0
  });
  const [isProcessingCards, setIsProcessingCards] = useState(false);
  const [lastAnswerCorrect, setLastAnswerCorrect] = useState(false);

  // Find the current level
  const currentLevel = levels.find(level => level.id === levelId);
  
  // Prepare cards for study session
  useEffect(() => {
    if (!cards.length) {
      navigate('/flashcards', { state: { message: 'Create some flashcards first!' } });
      return;
    }
    
    setIsProcessingCards(true);
    
    const prepareCards = async () => {
      try {
        // Filter cards that need to be reviewed (due date is past or today)
        const now = new Date();
        const dueCards = cards.filter(card => {
          const nextReview = new Date(card.nextReview);
          return nextReview <= now && !masteredCards.includes(card.id);
        });
        
        // If not enough due cards, add some random ones
        let selectedCards = [...dueCards];
        if (selectedCards.length < 5) {
          const remainingCards = cards.filter(card => !dueCards.some(dc => dc.id === card.id) && !masteredCards.includes(card.id));
          const randomCards = remainingCards.sort(() => 0.5 - Math.random()).slice(0, 5 - selectedCards.length);
          selectedCards = [...selectedCards, ...randomCards];
        }
        
        // Limit to 10 cards per session and shuffle them
        selectedCards = selectedCards.slice(0, 10).sort(() => 0.5 - Math.random());
        
        // Ensure we have at least one card in the session
        if (selectedCards.length === 0) {
          // If no cards are available, create a placeholder message card
          selectedCards = [{
            id: 'placeholder',
            type: 'classic',
            front: 'No cards to study',
            back: 'Create more flashcards or wait for cards to be due for review',
            nextReview: new Date().toISOString(),
          }];
        }
        
        // Process cards for Japanese content if needed
        const processedSelectedCards = await processJapaneseFlashcards(selectedCards);
        
        setSessionCards(processedSelectedCards);
        setSessionStats(prev => ({ ...prev, total: processedSelectedCards.length }));
        setIsProcessingCards(false);
      } catch (error) {
        console.error('Error preparing cards:', error);
        setSessionCards([]);
        setIsProcessingCards(false);
      }
    };
    
    prepareCards();
  }, [cards, masteredCards, levelId, navigate]);

  const currentCard = sessionCards[currentCardIndex];

  const handleShowAnswer = () => {
    setShowAnswer(true);
  };

  // Add special logic for Anki cards - they should never auto-advance
  const handleNextCard = (quality) => {
    if (!currentCard) return;
    
    // Record review with SM-2 algorithm
    dispatch(reviewFlashcard({ id: currentCard.id, quality }));
    
    // Track streak
    dispatch(increaseStreak());
    
    // Check if card is mastered (quality 5 means easy)
    if (quality === 5) {
      dispatch(masterCard(currentCard.id));
      setSessionStats(prev => ({ ...prev, mastered: prev.mastered + 1 }));
    }
    
    if (quality >= 3) {
      setSessionStats(prev => ({ ...prev, correct: prev.correct + 1 }));
      setLastAnswerCorrect(true);
    } else {
      setSessionStats(prev => ({ ...prev, incorrect: prev.incorrect + 1 }));
      setLastAnswerCorrect(false);
    }
    
    // Move to next card or complete session
    if (currentCardIndex < sessionCards.length - 1) {
      setCurrentCardIndex(prev => prev + 1);
      setShowAnswer(false);
      setSelectedOption(null);
      setUserInput('');
      setIsCorrect(null);
      // Reset last answer status when moving to next card
      setLastAnswerCorrect(false);
    } else {
      // Session completed
      dispatch(completeLevel(levelId));
      setSessionCompleted(true);
    }
  };

  const handleOptionSelect = (index) => {
    setSelectedOption(index);
    const isAnswerCorrect = index === currentCard.multipleChoice.correctIndex;
    setIsCorrect(isAnswerCorrect);
    setLastAnswerCorrect(isAnswerCorrect);
    
    // Remove auto-advance for multiple-choice
    // We'll always require clicking the continue button
  };

  const handleFillSubmit = (e) => {
    e.preventDefault();
    const isAnswerCorrect = userInput.toLowerCase() === currentCard.answer.toLowerCase();
    setIsCorrect(isAnswerCorrect);
    setShowAnswer(true);
    setLastAnswerCorrect(isAnswerCorrect);
    
    // Remove auto-advance for fill-in-the-blank
    // We'll always require clicking the continue button
  };

  // Add this method to extract furigana for a specific word
  const getAnswerFurigana = (answer, fullFuriganaMap) => {
    if (!answer || !fullFuriganaMap) return {};
    
    const answerFurigana = {};
    
    // Check if the exact answer has furigana in the map
    if (fullFuriganaMap[answer]) {
      answerFurigana[answer] = fullFuriganaMap[answer];
    } else {
      // If not, check if any key in the map is contained in the answer
      Object.keys(fullFuriganaMap).forEach(key => {
        if (answer.includes(key)) {
          answerFurigana[key] = fullFuriganaMap[key];
        }
      });
    }
    
    return answerFurigana;
  };

  const renderCard = () => {
    if (!currentCard) return null;
    
    switch (currentCard.type) {
      case 'classic':
        return (
          <ClassicCard>
            <CardFront>
              <CardContent>
                <FuriganaText text={currentCard.front} furigana={currentCard.frontFurigana} />
              </CardContent>
            </CardFront>
            
            {showAnswer ? (
              <>
                <CardBack>
                  <CardContent>
                    <FuriganaText text={currentCard.back} furigana={currentCard.backFurigana} />
                    
                    {/* Show example sentence if available */}
                    {currentCard.sentenceExample && (
                      <ExampleSentence>
                        <ExampleLabel>Example:</ExampleLabel>
                        <FuriganaText 
                          text={currentCard.sentenceExample}
                          furigana={currentCard.sentenceExampleFurigana}
                        />
                        {currentCard.sentenceExampleTranslation && (
                          <ExampleTranslation>"{currentCard.sentenceExampleTranslation}"</ExampleTranslation>
                        )}
                      </ExampleSentence>
                    )}
                  </CardContent>
                </CardBack>
                
                {/* Always show rating buttons for Anki-style cards - never auto-advance */}
                <RatingButtons>
                  <RatingButton color="#ff5252" onClick={() => handleNextCard(1)}>Again</RatingButton>
                  <RatingButton color="#ff9800" onClick={() => handleNextCard(2)}>Hard</RatingButton>
                  <RatingButton color="#4caf50" onClick={() => handleNextCard(3)}>Good</RatingButton>
                  <RatingButton color="#2196f3" onClick={() => handleNextCard(5)}>Easy</RatingButton>
                </RatingButtons>
              </>
            ) : (
              <ShowAnswerButton onClick={handleShowAnswer}>
                Show Answer
              </ShowAnswerButton>
            )}
          </ClassicCard>
        );
        
      case 'multiple':
        return (
          <MultipleChoiceCard>
            <CardFront>
              <CardContent>
                <FuriganaText text={currentCard.front} furigana={currentCard.frontFurigana} />
              </CardContent>
            </CardFront>
            
            <OptionsContainer>
              {currentCard.multipleChoice.options.map((option, index) => (
                <OptionButton
                  key={index}
                  selected={selectedOption === index}
                  correct={selectedOption !== null && index === currentCard.multipleChoice.correctIndex}
                  incorrect={selectedOption === index && index !== currentCard.multipleChoice.correctIndex}
                  disabled={selectedOption !== null}
                  onClick={() => selectedOption === null && handleOptionSelect(index)}
                >
                  {option}
                </OptionButton>
              ))}
            </OptionsContainer>

            {/* Show example sentence when answer is revealed */}
            {selectedOption !== null && currentCard.sentenceExample && (
              <ExampleSentenceContainer>
                <FuriganaText 
                  text={currentCard.sentenceExample}
                  furigana={currentCard.sentenceExampleFurigana}
                />
                {currentCard.sentenceExampleTranslation && (
                  <ExampleTranslation>"{currentCard.sentenceExampleTranslation}"</ExampleTranslation>
                )}
              </ExampleSentenceContainer>
            )}
            
            {/* Always show continue button when an answer is selected */}
            {selectedOption !== null && (
              <ContinueButtonContainer>
                <ContinueButton 
                  onClick={() => handleNextCard(isCorrect ? 4 : 2)}
                  color={isCorrect ? "#4caf50" : "#ff5252"}
                >
                  Continue
                </ContinueButton>
              </ContinueButtonContainer>
            )}
          </MultipleChoiceCard>
        );
        
      case 'fill':
        return (
          <FillBlankCard>
            <CardFront>
              <CardContent>
                {currentCard.sentence.split('___').map((part, index, array) => (
                  <React.Fragment key={index}>
                    <FuriganaText text={part} furigana={currentCard.sentenceFurigana} />
                    {index < array.length - 1 && (
                      <BlankSpace isAnswered={showAnswer}>
                        {showAnswer ? currentCard.answer : ''}
                      </BlankSpace>
                    )}
                  </React.Fragment>
                ))}
              </CardContent>
            </CardFront>
            
            {showAnswer ? (
              <>
                {isCorrect !== null && (
                  <FeedbackMessage correct={isCorrect}>
                    {isCorrect ? (
                      'Correct!'
                    ) : (
                      <>
                        Incorrect. The answer is:{' '}
                        <FuriganaText 
                          text={currentCard.answer} 
                          // Create specific furigana mapping just for the answer
                          furigana={getAnswerFurigana(currentCard.answer, currentCard.sentenceFurigana || {})}
                        />
                      </>
                    )}
                  </FeedbackMessage>
                )}
                
                {/* Always show continue button */}
                <RatingButtons>
                  <RatingButton 
                    color={isCorrect ? "#4caf50" : "#ff5252"} 
                    onClick={() => handleNextCard(isCorrect ? 4 : 2)}
                  >
                    Continue
                  </RatingButton>
                </RatingButtons>
              </>
            ) : (
              <FillForm onSubmit={handleFillSubmit}>
                <FillInput
                  type="text"
                  value={userInput}
                  onChange={(e) => setUserInput(e.target.value)}
                  placeholder="Type your answer..."
                  autoFocus
                />
                <FillSubmitButton type="submit" disabled={!userInput.trim()}>
                  Check
                </FillSubmitButton>
              </FillForm>
            )}
          </FillBlankCard>
        );
        
      default:
        return null;
    }
  };

  if (sessionCompleted) {
    return (
      <Container>
        <CompletionBanner>
          <CompletionIcon>🎉</CompletionIcon>
          <h2>Level Completed!</h2>
          <p>Great job! You've completed this study session.</p>
          
          <CompletionStats>
            <StatItem>
              <StatLabel>Cards Studied</StatLabel>
              <StatValue>{sessionStats.total}</StatValue>
            </StatItem>
            <StatItem>
              <StatLabel>Correct</StatLabel>
              <StatValue>{sessionStats.correct}</StatValue>
            </StatItem>
            <StatItem>
              <StatLabel>Incorrect</StatLabel>
              <StatValue>{sessionStats.incorrect}</StatValue>
            </StatItem>
            <StatItem>
              <StatLabel>Mastered</StatLabel>
              <StatValue>{sessionStats.mastered}</StatValue>
            </StatItem>
          </CompletionStats>
          
          <ButtonGroup>
            <PrimaryButton onClick={() => navigate('/study')}>
              Return to Level Map
            </PrimaryButton>
          </ButtonGroup>
        </CompletionBanner>
      </Container>
    );
  }

  if (isProcessingCards) {
    return (
      <Container>
        <LoadingContainer>
          <LoadingSpinner />
          <LoadingText>Preparing flashcards...</LoadingText>
        </LoadingContainer>
      </Container>
    );
  }

  return (
    <Container>
      {currentLevel && (
        <LevelHeader>
          <LevelName>{currentLevel.name}</LevelName>
          <ProgressIndicator>
            {Math.min(currentCardIndex + 1, sessionCards.length)} / {sessionCards.length}
          </ProgressIndicator>
        </LevelHeader>
      )}
      
      <CardContainer>
        {renderCard()}
      </CardContainer>
      
      <SessionControls>
        {/* Only show exit button if there's no continue button on the card already */}
        {currentCard && (
          (currentCard.type === 'multiple' && selectedOption === null) || 
          (currentCard.type === 'fill' && !showAnswer) || 
          (currentCard.type === 'classic' && !showAnswer)
        ) && (
          <SecondaryButton onClick={() => navigate('/study')}>
            Exit Session
          </SecondaryButton>
        )}
      </SessionControls>
    </Container>
  );
};

const Container = styled.div`
  padding: 16px;
  max-width: 600px;
  margin: 0 auto;
`;

const LevelHeader = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
`;

const LevelName = styled.h2`
  font-size: 20px;
  margin: 0;
  color: ${props => props.theme.text};
`;

const ProgressIndicator = styled.div`
  background: ${props => props.theme.backgroundDark};
  padding: 6px 12px;
  border-radius: 16px;
  font-size: 14px;
  font-weight: 600;
`;

const CardContainer = styled.div`
  background: ${props => props.theme.backgroundLight};
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  margin-bottom: 24px;
  min-height: 300px;
  display: flex;
  flex-direction: column;
`;

const ClassicCard = styled.div`
  display: flex;
  flex-direction: column;
  flex: 1;
`;

const MultipleChoiceCard = styled.div`
  display: flex;
  flex-direction: column;
  flex: 1;
`;

const FillBlankCard = styled.div`
  display: flex;
  flex-direction: column;
  flex: 1;
`;

const CardFront = styled.div`
  padding: 24px;
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  border-bottom: 1px solid ${props => props.theme.border};
`;

const CardBack = styled.div`
  padding: 24px;
  background: ${props => props.theme.backgroundDark};
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  min-height: 100px;
`;

const CardContent = styled.div`
  font-size: 24px;
  line-height: 1.5;
`;

const BlankSpace = styled.span`
  display: inline-block;
  min-width: 80px;
  padding: 0 8px;
  margin: 0 4px;
  border-bottom: 2px solid ${props => props.isAnswered ? props.theme.success : props.theme.primary};
  color: ${props => props.isAnswered ? props.theme.success : 'transparent'};
  font-weight: bold;
`;

const ShowAnswerButton = styled.button`
  padding: 16px;
  background: ${props => props.theme.primary};
  color: white;
  border: none;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: background 0.2s;
  
  &:hover {
    background: ${props => props.theme.primaryDark};
  }
`;

const RatingButtons = styled.div`
  display: flex;
  border-top: 1px solid ${props => props.theme.border};
`;

const RatingButton = styled.button`
  flex: 1;
  padding: 16px;
  border: none;
  background: ${props => props.color};
  color: white;
  font-weight: 600;
  cursor: pointer;
  transition: opacity 0.2s;
  
  &:hover {
    opacity: 0.9;
  }
`;

const OptionsContainer = styled.div`
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding: 16px;
`;

const OptionButton = styled.button`
  padding: 12px 16px;
  border: 2px solid ${props => {
    if (props.correct) return props.theme.success;
    if (props.incorrect) return props.theme.error;
    if (props.selected) return props.theme.primary;
    return props.theme.border;
  }};
  background: ${props => {
    if (props.correct) return `${props.theme.success}20`;
    if (props.incorrect) return `${props.theme.error}20`;
    if (props.selected) return `${props.theme.primary}20`;
    return 'transparent';
  }};
  border-radius: 8px;
  font-size: 16px;
  text-align: left;
  cursor: ${props => props.disabled ? 'default' : 'pointer'};
  transition: all 0.2s;
  
  &:hover {
    background: ${props => {
      if (props.disabled) {
        if (props.correct) return `${props.theme.success}20`;
        if (props.incorrect) return `${props.theme.error}20`;
        if (props.selected) return `${props.theme.primary}20`;
        return 'transparent';
      }
      return props.theme.backgroundDark;
    }};
  }
`;

const FillForm = styled.form`
  display: flex;
  padding: 16px;
  gap: 8px;
`;

const FillInput = styled.input`
  flex: 1;
  padding: 12px;
  border: 1px solid ${props => props.theme.border};
  border-radius: 4px;
  font-size: 16px;
`;

const FillSubmitButton = styled.button`
  padding: 12px 24px;
  background: ${props => props.disabled ? props.theme.backgroundDark : props.theme.primary};
  color: ${props => props.disabled ? props.theme.textLight : 'white'};
  border: none;
  border-radius: 4px;
  font-weight: 600;
  cursor: ${props => props.disabled ? 'not-allowed' : 'pointer'};
`;

const FeedbackMessage = styled.div`
  padding: 16px;
  text-align: center;
  font-weight: 600;
  background: ${props => props.correct ? `${props.theme.success}20` : `${props.theme.error}20`};
  color: ${props => props.correct ? props.theme.success : props.theme.error};
  
  /* Add this to handle furigana text that could appear */
  span, ruby {
    color: inherit;
    font-weight: inherit;
  }
`;

const ExampleSentence = styled.div`
  margin-top: 16px;
  padding-top: 12px;
  border-top: 1px dashed ${props => props.theme.border};
  font-size: 0.9em;
  font-style: italic;
`;

const ExampleLabel = styled.div`
  font-weight: 600;
  margin-bottom: 6px;
  font-size: 14px;
  color: ${props => props.theme.textLight};
`;

const ExampleSentenceContainer = styled.div`
  margin: 12px 16px;
  padding: 10px;
  background: ${props => props.theme.backgroundDark};
  border-radius: 6px;
  font-style: italic;
  text-align: center;
  font-size: 0.9em;
`;

const ExampleTranslation = styled.div`
  font-size: 0.9em;
  color: ${props => props.theme.textLight};
  margin-top: 6px;
  font-style: italic;
`;

const SessionControls = styled.div`
  display: flex;
  justify-content: center;
`;

const SecondaryButton = styled.button`
  padding: 12px 24px;
  background: transparent;
  border: 1px solid ${props => props.theme.border};
  border-radius: 4px;
  font-size: 16px;
  cursor: pointer;
  transition: background 0.2s;
  
  &:hover {
    background: ${props => props.theme.backgroundDark};
  }
`;

const CompletionBanner = styled.div`
  text-align: center;
  padding: 32px;
  background: ${props => props.theme.backgroundLight};
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  
  h2 {
    font-size: 24px;
    margin: 12px 0;
  }
  
  p {
    font-size: 16px;
    color: ${props => props.theme.textLight};
    margin-bottom: 24px;
  }
`;

const CompletionIcon = styled.div`
  font-size: 48px;
  margin-bottom: 16px;
`;

const CompletionStats = styled.div`
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 16px;
  margin: 24px 0;
`;

const StatItem = styled.div`
  background: ${props => props.theme.background};
  border-radius: 8px;
  padding: 16px;
`;

const StatLabel = styled.div`
  font-size: 12px;
  color: ${props => props.theme.textLight};
  margin-bottom: 8px;
`;

const StatValue = styled.div`
  font-size: 24px;
  font-weight: 600;
`;

const ButtonGroup = styled.div`
  margin-top: 32px;
`;

const PrimaryButton = styled.button`
  padding: 12px 24px;
  background: ${props => props.theme.primary};
  color: white;
  border: none;
  border-radius: 4px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: background 0.2s;
  
  &:hover {
    background: ${props => props.theme.primaryDark};
  }
`;

const LoadingContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 60px 20px;
`;

const LoadingSpinner = styled.div`
  width: 40px;
  height: 40px;
  border: 4px solid ${props => props.theme.backgroundDark};
  border-top-color: ${props => props.theme.primary};
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-bottom: 16px;
  
  @keyframes spin {
    to { transform: rotate(360deg); }
  }
`;

const LoadingText = styled.div`
  font-size: 16px;
  color: ${props => props.theme.textLight};
`;

const ContinueButtonContainer = styled.div`
  display: flex;
  justify-content: center;
  margin-top: 16px;
  padding: 0 16px 16px;
`;

const ContinueButton = styled.button`
  padding: 10px 24px;
  background: ${props => props.color || props.theme.primary};
  color: white;
  border: none;
  border-radius: 4px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
`;

const AutoProgressIndicator = styled.div`
  text-align: center;
  color: ${props => props.theme.textLight};
  font-size: 14px;
  font-style: italic;
  padding: 10px 0;
  border-top: 1px solid ${props => props.theme.border};
`;

export default StudySessionPage;
