import React, { useState, useEffect } from 'react';
import { useSelector } from 'react-redux';
import styled from 'styled-components';
import { isDictionaryInstalled } from '../../services/dictionaryLoader';
import { tokenizeJapanese } from '../../services/japaneseService';

/**
 * Renders Japanese text with furigana reading aid
 * 
 * @param {Object} props
 * @param {string} props.text - The Japanese text to display
 * @param {Object} props.furigana - Optional pre-computed furigana mapping
 * @param {boolean} props.automatic - Whether to automatically generate furigana
 */
const FuriganaText = ({ text, furigana = {}, automatic = true }) => {
  const { showFurigana } = useSelector(state => state.settings);
  // Initialize tokens with the plain text so it never flashes "..."
  const [tokens, setTokens] = useState([{ text: text || '', reading: null }]);
  const [dictionaryAvailable, setDictionaryAvailable] = useState(false);
  const [processingError, setProcessingError] = useState(false);

  useEffect(() => {
    checkDictionary();
  }, []);

  useEffect(() => {
    // Safety check to avoid processing if already errored for this text
    if (processingError) return;
    
    // If there's no text, we can immediately set tokens to empty
    if (!text) {
      setTokens([{ text: '', reading: null }]);
      return;
    }
    
    // Add a safety limit for text length to prevent processing huge strings
    if (text && text.length > 1000) {
      console.warn('Text too long for furigana processing, limiting to plain text display');
      setTokens([{ text, reading: null }]);
      return;
    }
    
    // Use a fallback timeout to ensure component doesn't hang
    const timeoutId = setTimeout(() => {
      console.warn('Furigana processing timeout, falling back to plain text');
      setTokens([{ text, reading: null }]);
      setProcessingError(true);
    }, 2000);

    processText()
      .finally(() => clearTimeout(timeoutId));
  }, [text, furigana, automatic, dictionaryAvailable, showFurigana]);

  const checkDictionary = async () => {
    try {
      const isAvailable = await isDictionaryInstalled();
      setDictionaryAvailable(isAvailable);
    } catch (error) {
      console.error('Error checking dictionary status:', error);
      setDictionaryAvailable(false);
    }
  };

  const processText = async () => {
    // If furigana is disabled or text is empty, just show plain text
    if (!showFurigana || !text) {
      setTokens([{ text, reading: null }]);
      return;
    }

    try {
      // Handle pre-computed furigana with safety
      if (furigana && typeof furigana === 'object' && Object.keys(furigana).length > 0) {
        const processedTokens = processWithProvidedFurigana(text, furigana);
        setTokens(processedTokens);
      } 
      // Only try automatic processing if dictionary is available
      else if (automatic && dictionaryAvailable) {
        const processedTokens = await tokenizeJapanese(text);
        setTokens(processedTokens || [{ text, reading: null }]);
      } 
      // Fallback to plain text
      else {
        setTokens([{ text, reading: null }]);
      }
    } catch (error) {
      console.error('Error processing furigana:', error);
      // Fallback to plain text
      setTokens([{ text, reading: null }]);
      setProcessingError(true);
    }
  };

  // Process text with provided furigana mappings
  const processWithProvidedFurigana = (text, furiganaMap) => {
    if (!text) return [];
    
    // Find all words that have furigana readings
    const words = Object.keys(furiganaMap).sort((a, b) => b.length - a.length);
    
    if (words.length === 0) return [{ text, reading: null }];
    
    let result = [];
    let remaining = text;
    
    while (remaining.length > 0) {
      let matched = false;
      
      // Try to match the longest word first
      for (const word of words) {
        if (remaining.startsWith(word)) {
          // Add the word with its furigana
          result.push({
            text: word,
            reading: furiganaMap[word]
          });
          
          // Remove the matched word from the remaining text
          remaining = remaining.slice(word.length);
          matched = true;
          break;
        }
      }
      
      // If no match was found, take the first character as-is
      if (!matched) {
        result.push({
          text: remaining[0],
          reading: null
        });
        remaining = remaining.slice(1);
      }
    }
    
    return result;
  };

  // Merge adjacent tokens without furigana for better display
  const mergeAdjacentPlainTokens = (tokens) => {
    if (!tokens || tokens.length <= 1) return tokens;
    
    const result = [];
    let currentPlainText = '';
    
    for (const token of tokens) {
      // If this token has no reading (furigana)
      if (!token.reading) {
        currentPlainText += token.text;
      } else {
        // If we've accumulated plain text, add it first
        if (currentPlainText) {
          result.push({ text: currentPlainText, reading: null });
          currentPlainText = '';
        }
        // Add the token with furigana
        result.push(token);
      }
    }
    
    // Add any remaining plain text
    if (currentPlainText) {
      result.push({ text: currentPlainText, reading: null });
    }
    
    return result;
  };

  // Make component resilient to bad input
  const safeProcessedTokens = mergeAdjacentPlainTokens(tokens) || [{ text: text || '', reading: null }];
  
  return (
    <FuriganaContainer>
      {safeProcessedTokens.map((token, index) => (
        token.reading ? (
          <Ruby key={index}>
            {token.text}
            <Rt>{token.reading}</Rt>
          </Ruby>
        ) : (
          <span key={index}>{token.text}</span>
        )
      ))}
    </FuriganaContainer>
  );
};

// Styled components
const FuriganaContainer = styled.span`
  display: inline;
  line-height: 1.6;
`;

const Ruby = styled.ruby`
  ruby-align: center;
`;

const Rt = styled.rt`
  font-size: 0.6em;
  line-height: 1;
  text-align: center;
  color: ${props => props.theme.textLight};
`;

export default FuriganaText;
